import React from 'react';
import {FormGroup, Label, /* Input */ } from 'reactstrap';
//import Select from 'react-select';
//import 'react-select/dist/react-select.css';
import {store} from '../../index';
import cookie from 'react-cookies';
import {connect} from 'react-redux';

import PropTypes from 'prop-types';
//import Async, { makeAsyncSelect } from 'react-select/async';

import AsyncSelect from "react-select/async";
import { components } from 'react-select';
import { updateSectionItem } from '../../storage/actions';
import { fetchPreviewData as fetchPreviewDataAction } from '../../storage/thunkActions';
import _ from 'lodash';



let URL_PREFIX = '';

const Option = props => {
    const {labelKey, valueKey} = props.selectProps;
	let keysData = Object.keys(props.data).filter(e=>!['id','_str_name'].includes(e));

	const valuesData = [];
	const id = props.data[valueKey];
    keysData.forEach((k,i)=>{ 
        const labelFld = props.data[k]['str'];
		const valFld = props.data[k]['val'];
		
		valuesData.push(<small key={labelFld+'_opt_'+i+'_'+id}><strong>{labelFld}</strong>:{valFld}</small>)
		if(i<keysData.length)valuesData.push(<br key={"br_opt_"+i+'_'+id} />)
    }) 


    return (
        <components.Option {...props} key={props.data.id} >
            {props.data[labelKey]}<br/>
            {valuesData}
        </components.Option>
    )}; 


const customStyles = {
	indicatorSeparator: base => ({
		...base,
		display: 'none'
	}),
	/* dropdownIndicator: base => ({
		...base,
		display: 'none'
	}), */
	
}


class ForeignKey extends React.Component {
    constructor(props){
			super(props);
			const selected_operator = props.fieldDef ? props.fieldDef.selected_operator : false;
			this.operatorMulti = ['fk_in','fk_not_in'];
			this.state = {
				//valid: ((props.storeValue === "" || !Boolean(props.storeValue.id)) && this.props.fieldDef.required) ? false : true,
				msg: "",
				value: props.storeValue || '',
				labelKey:"_str_name",
				valueKey:"id",
				isMulti :this.operatorMulti.includes(selected_operator),
				
			};
			this.handleChange = this.handleChange.bind(this);
			this.getOptions = this.getOptions.bind(this);
			
	}
	
	componentDidMount(){
		let altThis= this;
		
		window.addEventListener('message', function(event){
			try{
				let data = JSON.parse(event.data);
				if(data.fieldId === altThis.props.fieldDef.name){
					let valueEvent = data.value;
					let newValue =  _.cloneDeep(altThis.state.value);
					if(altThis.state.isMulti){
						if(!newValue || newValue.length===0)newValue=[];
						let finded = newValue.find(e=>e[altThis.state.valueKey] === valueEvent[altThis.state.valueKey])
						if(!finded)newValue.push(valueEvent);
					}else{
						newValue = valueEvent;
					}
					altThis.handleChange(newValue);
				}
			}catch(error){
				//console.log('addEventListener=> ',error);
			}
		});
	}

	componentWillReceiveProps(nextProps){
		const isMulti = this.operatorMulti.includes(nextProps.selected_operator);
		if(this.state.isMulti !== isMulti){
			let stateValue = this.state.value;
			let newValue = [];
			if(stateValue && _.isArray(stateValue) && stateValue.length>0 && !isMulti)newValue=stateValue[0];
			if(isMulti && stateValue)newValue.push(stateValue)
			
			this.setState({isMulti},()=>{
				this.handleChange(newValue);
			})
		}
		
		/* let newValue = nextProps.storeValue;
		let val = {id:'', _str_name:''};
		if(newValue){
			val.id = newValue.id;
			val._str_name = newValue._str_name;
		}
		this.setState({value:newValue});
		
		if (newValue && newValue !== this.state.value) {
			if(this.props.fieldDef.required && !Boolean(val.id)){
				this.setState({valid: false});
			}else if(!this.state.valid){
				this.setState({valid: true});
			}
			
		} */
	}


    
    handleChange(newValue){
		this.setState({value: newValue},()=>{
			this.props.updateSectionItem(this.props.storeSection, this.props.index, this.props.property, newValue )
		})
    }

    getOptions = (input: string) => {
        if (!input || input.length <= 3) {
			return Promise.resolve([]);
		}
        return fetch(URL_PREFIX + '/search_records_rel_select/' + this.props.fieldDef.module_name + '/', {
			method: 'POST',
			credentials: "same-origin",
            headers: {
				"Accept": "application/json",
                "X-CSRFToken": cookie.load('csrftoken'),
				'X-Requested-With': 'XMLHttpRequest'
				
            },
			body: JSON.stringify({'keyword': input, agent: 'react-coreapp', 'store': store.getState() })})
          .then((response) => {
            return response.json();
          }).then((json) => {
            return json ;
          });
      };



    promiseOptions = inputValue =>
        new Promise(resolve => {
            setTimeout(() => {
            resolve(this.getOptions(inputValue));
        }, 1000);
	});
	

	  
	renderOption(optionvalue) {
        let data = [];
        for (let key in optionvalue) {
            if (!['id','_str_name'].includes(key)) {
                data.push( <span key={key}> <b> {optionvalue[key]['str']}:</b>{optionvalue[key]['val']}<br/> </span>);
            }
        }
        return ( <div> {data} </div>);
	}
	

    renderValue(newValue){
        return <div>{newValue._str_name}</div>
	}
	


    hackFilterOptions(options, filterString, values){
        /** Por defecto el componente react-select busca sobre los resultados del servidor, el problema es que lo hace solo en el 'label' */
        return options;
    }
	
	
	openWindowSearch= () => {
		let fieldName = (this.props.fieldDef.name) ? this.props.fieldDef.name : "";
		let url_search_window = URL_PREFIX + "/" +  this.props.fieldDef.module_name + "/popup_index/?fieldId=" + fieldName;
		window.open(url_search_window, 'popup', 'width=800,height=600,left=10,top=10')
	}


	noOptionsMessage = () => {
		return "No hay Registros"
	}

	dropdownIndicator = (innerProps, innerRef) => {
		let isDisabled = this.props.isDisabled ? true : false;	
		return (
			<div className={""}>
				<button type="button" disabled={isDisabled} className={"btn btn-transparent"} 
					onClick={this.openWindowSearch}>
					
					<i className={"fa fa-search"} ></i>
				</button>
			</div>
		)
	}

	
	render(){
		let fieldName = (this.props.fieldDef.name) ? this.props.fieldDef.name : "";
		let fieldClass = (this.props.fieldDef.class) ? this.props.fieldDef.class : "";
		let fieldLabel = (this.props.fieldDef.label) ? this.props.fieldDef.label : "";
		let printLabel = (this.props.fieldDef.printLabel) ? this.props.fieldDef.printLabel : true;		
		let fieldId = 'id_' + fieldName;
		let fieldPlaceHolder = this.props.fieldDef.placeHolder ? this.props.fieldDef.placeHolder : '';
		let pure_field = this.props.pureField ? true : false;
		let isDisabled = this.props.isDisabled ? true : false;	
		const {isMulti} = this.state;

		let field_jsx = (

						<div 
							/* className={classRequired+" "+classTooltip + " row no-gutters"} title={tooltip} */
						>
							<AsyncSelect 
								isMulti={isMulti}
								styles={customStyles}
                                loadOptions={this.promiseOptions}
                                cacheOptions 
                                isClearable
                                
                                disabled={isDisabled} 
                                filterOptions={this.hackFilterOptions} 
                                autoload={false} 
                                //ref='dmesa' 
                                labelKey={this.state.labelKey} 
                                valueKey={this.state.valueKey}
								components={{Option,DropdownIndicator: this.dropdownIndicator }}
								dropdownIndicator = {this.dropdownIndicator}
								//formatOptionLabel={this.renderOption}
								getOptionLabel={(option)=>{
									return option[this.state.labelKey] || '';
								}}
								getOptionValue={opt => opt[this.state.valueKey]}
								isDisabled={isDisabled}
								
								//menuPosition="fixed"
								//menuShouldBlockScroll={true}
								//closeMenuOnScroll={false}
								//menuIsOpen={true}
								//inputValue="_str_name"
								noOptionsMessage={this.noOptionsMessage}
                                name={fieldName}  
                                ignoreAccents={false} 
                                id={fieldId} 
                                placeholder={fieldPlaceHolder} 
                                value={this.state.value} 
                                className={fieldClass + " col  clearIndicator"} 
								//onChange={(option) => this.handleChange(option)}
								onChange={this.handleChange}
                                
                                //resetValue={deleteValue} 
							/>
							
							
							
							
							
							{/* <Input type='hidden' name={fieldName} id={fieldId} value={(this.props.storeValue['id']) ? this.props.storeValue['id']: '' } /> */}
						</div>
						);
		if(pure_field){
			return field_jsx;
		}else return (
			<FormGroup>
				<Label for={fieldId}>{printLabel ? fieldLabel : ''}</Label>
				{field_jsx}
			</FormGroup>
		);
	}
}

ForeignKey.propTypes = {
    fieldDef: PropTypes.object.isRequired,
    //onChangePage: PropTypes.func.isRequired,
    //initialPage: PropTypes.number
};


const mapStateToProps = state => {
    return {};
};
  
const mapDispatchToProps = dispatch => {
    return {
		'updateSectionItem': async (section, index, property, value) => {
			await dispatch(updateSectionItem(section, index, property, value));
			dispatch(fetchPreviewDataAction());
		}
	}  
};

export default connect(mapStateToProps, mapDispatchToProps)(ForeignKey);


