import moment from 'moment'

import {
    sample,
    random,
    cloneDeep,
    get
} from 'lodash'

import {
    charFieldMeta,
    integerFieldMeta,
    floatFieldMeta,
    dateTimeFieldMeta,
    dateFieldMeta,
    booleanFieldMeta,
    monthsMap
} from './constants'

function getJSONAttribute(element, attr_name, default_value) {
    try {
        return JSON.parse(element.getAttribute(attr_name)) || default_value;
    } catch (error) {
        return default_value;
    }
}

function formatFieldParameter(field_path, field_name) {
    field_name = field_name.toLowerCase();
    field_path = field_path.toLowerCase();
    let list_path = field_path.split("::");
    if (list_path.length <= 1) {
        return field_name;
    }
    list_path.shift();
    return list_path.join('__') + '__' + field_name;
}

function updateFieldKeyName(field_list) {
    for (let index = 0; index < field_list.length; index++) {
        const field = field_list[index];
        field.key_name = formatFieldParameter(field.path, field.name);
    }
}

function defaultJSONParse(str, default_value) {
    try {
        if (typeof str === "string") {
            return JSON.parse(str) || default_value;
        }
        return str || default_value;

    } catch (error) {
        console.log("error");
        return default_value;
    }
}

function arrayEval(array) {
    try {
        if (typeof array === "string") {
            return JSON.parse(array) || [];
        }
        return array || [];

    } catch (error) {
        console.log("error");
        return [];
    }
}

function getSelectedGroupedOption(array, current_value) {
    if (!array) return null;
    for (let index = 0; index < array.length; index++) {
        const element = array[index];
        if (element.options && element.options.length > 0) {
            let value = element.options.find((e) => { return e.value === current_value });
            if (value) {
                return value;
            }
        }
    }

    return null;
}

function isValidDate(d) {
    return d instanceof Date && !isNaN(d);
}

function numberFormat(num, digits, large_format) {
    var si = [
        { value: 1, symbol: "" },
        { value: 1E3, symbol: "k" },
        { value: 1E6, symbol: "M" },
        { value: 1E9, symbol: "G" },
        { value: 1E12, symbol: "T" },
        { value: 1E15, symbol: "P" },
        { value: 1E18, symbol: "E" }
    ];
    var rx = /\.0+$|(\.[0-9]*[1-9])0+$/;
    var i;
    for (i = si.length - 1; i > 0; i--) {
        if (num >= si[i].value) {
            break;
        }
    }
    if (large_format) {
        try {
            return num.toFixed(digits).replace(/(\d)(?=(\d{3})+\.)/g, '$1,');
        } catch (error) {
            return '';
        }

    }
    return (num / si[i].value).toFixed(digits).replace(rx, "$1") + si[i].symbol;
}

function defOrFactory (defValue)  {
    return function (value) {
        return !!value ? value : defValue
    }
}

const defOrEmptyStr = defOrFactory('')

function props (val) {
    return val
}

function isDate(dateStr) {
	return !!dateStr && moment(dateStr).isValid()
}

function getMonthLabel(number) {
    return get(monthsMap, number+'', number+'')
}

function month(dateStr) {
    if (!isDate(dateStr))  {
        return ''
    }

    try {
        return (new Date(dateStr)).getMonth() + 1
    } catch (e) {
        return ''
    }
}

function year(dateStr) {
    if (!isDate(dateStr))  {
        return ''
    }

    try {
        return (new Date(dateStr)).getFullYear()
    } catch (e) {
        return ''
    }
}

function date(dateStr) {
    if (!isDate(dateStr))  {
        return ''
    }

    try {
        return (new Date(dateStr)).getDate()
    } catch (e) {
        return ''
    }
}

function day(dateStr) {
    if (!isDate(dateStr))  {
        return ''
    }

    try {
        return (new Date(dateStr)).getDay()
    } catch (e) {
        return ''
    }
}

function hour(dateStr) {
    if (!isDate(dateStr))  {
        return ''
    }

    try {
        return (new Date(dateStr)).getHours()
    } catch (e) {
        return ''
    }
}

function minute(dateStr) {
    if (!isDate(dateStr))  {
        return ''
    }

    try {
        return (new Date(dateStr)).getMinutes()
    } catch (e) {
        return ''
    }
}

function period(dateStr) {
    return `${year(dateStr)}${(month(dateStr)).toString().padStart(2,"0")}`
}

function getDatePart(dateStr) {
    if (!isDate(dateStr)) {
        return ''
    }
    return moment(dateStr).startOf('day').format()
}

function getTimePart(dateStr) {
    if (!isDate(dateStr)) {
        return ''
    }
    return moment(dateStr).startOf('day').format("hh:mm:ss")
}

function dateAdd(dateStr, amount, period) {
    if (!isDate(dateStr)) {
        return ''
    }
    return moment(dateStr).add(amount, period).format()
}

function dateAddFact(period) {
    return function (dateStr, amount) {
        if (!isDate(dateStr)) {
            return ''
        }
        return moment(dateStr).add(amount, period).format()
    } 
}

const addDay = dateAddFact('day')
const addMonth = dateAddFact('month')
const addYear = dateAddFact('year')

function dateSubstractFact(period) {
    return function (dateStr, amount) {
        if (!isDate(dateStr)) {
            return ''
        }
        return moment(dateStr).subtract(amount, period).format()
    } 
}

const substractDay = dateSubstractFact('day')
const substractMonth = dateSubstractFact('month')
const substractYear = dateSubstractFact('year')

function now() {
    return moment().format()
}

function diffFact(period) {
    return function (dateStrA, dateStrB) {
        if (!isDate(dateStrA)) {
            return ''
        }

        if (!isDate(dateStrB)) {
            return ''
        }

        return moment(dateStrA).diff(moment(dateStrB), period)
    } 
}

const diffDays = diffFact('days')
const diffMonths = diffFact('months')
const diffYears = diffFact('years')

function changeFormat (dateStr, format) {
    if (!isDate(dateStr)) {
        return ''
    }

    return moment(dateStr).format(format)
}

const isEmptyFact = testFn => val => testFn(val) ? true : false
const isEmptyObj = isEmptyFact(obj => !Object.keys(obj).length)

function reduceToObj (obj, curr) {
    return { ...obj, ...curr }
}

function generateString() {
    let s = "abcdefghijklmnopqrstuvwxyzABCDEFGHIJKLMNOPQRSTUVWXYZ";
    return Array(5).join().split(',').map(function() { return s.charAt(Math.floor(Math.random() * s.length)); }).join('');
}

function generateInteger() {
    return random(1, 1000)   
}

function generateFloat() {
    let min = 0.1
    let max = 10000
    return Math.random() * (max - min) + min;   
}

function generateDateTime() {
    return moment().format()   
}

function generateDate() {
    return moment().format('YYYY-mm-DD')   
}

function generateBoolean() {
    return sample([true, false])
}

function generateSample (type) {
    switch (type) {
        case 'CharField':
            return generateString()
        case 'IntegerField':
        case 'PositiveIntegerField':
            return generateInteger()
        case 'FloatField':
        case 'CurrencyField':
        case 'DecimalField':
            return generateFloat()
        case 'DateTimeField':
            return generateDateTime()
        case 'DateField':
            return generateDate()
        case 'BooleanField':
            return generateBoolean()
        default:
            return generateString()
    }
}

function toType (obj) {
    //Typeof. determina en forma aproximada el tipo de obj
    return ({}).toString.call(obj).match(/\s([a-zA-Z]+)/)[1].toLowerCase()
}

function isInt(n) {
    return n % 1 === 0;
 }

function getType (something) {
    const type = toType(something)
    if (type === "string") {
        //could be parse into a date
        if (something.length >= 10 && isDate(something)) {
            if (something.length > 10) {
                return 'DateTimeField'
            }  else {
                return 'DateField'
            }
        } else {
            return 'CharField'
        }
    } else if (type === "number") {
        if (isInt(something)) {
            return 'IntegerField'
        } else {
            return 'FloatField'
        }
    } else if (type === "boolean")  {
        return 'BooleanField'
    } else if (type === "undefined") {
        return 'CharField'
    } else {
        return false
    }
}

function buildMeta (type) {
    switch (type) {
        case 'CharField':
            return cloneDeep(charFieldMeta)
        case 'IntegerField':
            return cloneDeep(integerFieldMeta)
        case 'FloatField':
            return cloneDeep(floatFieldMeta)
        case 'DateTimeField':
            return cloneDeep(dateTimeFieldMeta)
        case 'DateField':
            return cloneDeep(dateFieldMeta)
        case 'BooleanField':
            return cloneDeep(booleanFieldMeta)
        default:
            return cloneDeep(charFieldMeta)
    }
}

const formulasLib = {
    props,
    year,
    month,
    getMonthLabel,
    date,
    day,
    hour,
    minute,
    period,
    getDatePart,
    getTimePart,
    dateAdd,
    addDay,
    addMonth,
    addYear,
    substractDay,
    substractMonth,
    substractYear,
    now,
    diffDays,
    diffMonths,
    diffYears,
    changeFormat,
}

export const SLUG = function (str = '') {
    str = str.replace(/^\s+|\s+$/g, '') // trim
    str = str.toLowerCase()

    // remove accents, swap ñ for n, etc
    let from = "ãàáäâẽèéëêìíïîõòóöôùúüûñç·/_,:;"
    let to = "aaaaaeeeeeiiiiooooouuuunc------"
    for (let i = 0, l = from.length; i < l; i++) {
        str = str.replace(new RegExp(from.charAt(i), 'g'), to.charAt(i))
    }

    str = str.replace(/[^a-z0-9 -]/g, '') // remove invalid chars
        .replace(/\s+/g, '_') // collapse whitespace and replace by -
        .replace(/-+/g, '_')  // collapse dashes

    return str
}

export const generateName = function (str = '') {
    let name = SLUG(str);
    name = "cstm_formula_"+name
    name = name.replace(/_+/g, '_').substr(0,50)
    // Eliminar los _ al final de campo, para evitar error de migraciones
    return name.slice(name.length - 1) === "_" ? name.slice(0, -1) : name
}

export { 
    getJSONAttribute, 
    formatFieldParameter, 
    updateFieldKeyName, 
    arrayEval, 
    getSelectedGroupedOption, 
    isValidDate, 
    defaultJSONParse, 
    numberFormat,
    getType,
    generateSample,
    buildMeta,
    isEmptyObj,
    reduceToObj,
    formulasLib,
};