import React, { Component } from 'react';
import { connect } from 'react-redux';
import Select from 'react-select';
import { Button, Col, Form, FormGroup, Input, Label, Modal, ModalBody, ModalFooter, Row } from 'reactstrap';
import { toggleChartsModal as toggleChartsModalAction, addChart, setEditChartIndex, editChart } from '../storage/actions';

import OpalChart from './Chart';
import { ReactComponent as EmptyImage } from '../images/empty_dash.svg';
import cookie from 'react-cookies';
import ColorPicker from './ColorPicker';

const charts_options = [
  { value: "bar", label: "Barras" },
  { value: "stacked_bar", label: "Barras Apiladas" },
  { value: "grouped_bar", label: "Barras Agrupadas" },
  { value: "horizontal_bar", label: "Barras Horizontales" },
  { value: "horizontal_stacked_bar", label: "Barras Horizontales Apiladas" },
  { value: "horizontal_grouped_bar", label: "Barras Horizontales Agrupadas" },
  { value: "donut", label: "Dona" },
  { value: "line", label: "Líneas" },
  { value: "area", label: "Área" },
  { value: "funnel", label: "Funnel" },
  { value: "numeric", label: "Numérico" }
]; //TODO: Colocar imagenes, pedir los svg

const operator_options = [
  { value: "count", label: "Contar" },
  { value: "sum", label: "Sumar" },
  { value: "avg", label: "Promedio" },
  { value: "max", label: "Maximo" },
  { value: "min", label: "Mínimo" }
]; //TODO: Obtener desde el backend


class ChartsModal extends Component {


  constructor(props) {
    super(props);
    this.initial_state = {
      title: '',
      type: '',
      xaxis: '',
      yaxis: [],
      operator: '',
      order_options: [],
      order_options_value: [],
      value_field: '',
      background_color: '#fff',
      text_color: "#000"
    }

    this.state = { ...this.initial_state };
  }

  getOptionsEnums = async (selectfield_params) => {
    if (!selectfield_params) {
      this.setState({
        order_options: []
      });
      return;
    }

    let data = {
      enum: selectfield_params.list_name,
      fieldDef: selectfield_params
    }

    fetch('/entry_point/get_options_enum/', {
      method: 'POST',
      credentials: "same-origin",
      headers: {
        "Accept": "application/json",
        "X-CSRFToken": cookie.load('csrftoken'),
        'X-Requested-With': 'XMLHttpRequest'
      },
      body: JSON.stringify(data)
    })
      .then(res => {
        return res.json()
      })
      .then(res => {
        if (res.status && res.options) {
          this.setState({
            order_options: res.options.map(field => { return { value: field.key, label: field.label } })
          });
        }
      })
      .catch(error => {
        console.log("[getOptionsEnums] Error", error);
        this.setState({
          order_options: []
        });
      });
  }

  componentWillReceiveProps(new_props) {
    if (new_props.edit_chart_index !== this.props.edit_chart_index) {
      try {
        let chart_def = new_props.chart_list[new_props.edit_chart_index];
        this.setState({ ...chart_def });
      } catch (error) {
        
      }
      
    }
  }

  onSave = async () => {
    const { edit_chart_index } = this.props;
    if(typeof edit_chart_index !== "undefined" && edit_chart_index !== null){
      await this.props.editChart(edit_chart_index, this.state);
    }else{
      await this.props.addChart(this.state);
    }
    this.closeModal();
    // if (this.props.dashlet_id) {

    //   fetch('/OpalUiGridClass/containers_save_options/', {
    //     method: 'POST',
    //     credentials: "same-origin",
    //     headers: {
    //       "Accept": "application/json",
    //       "X-CSRFToken": cookie.load('csrftoken'),
    //       'X-Requested-With': 'XMLHttpRequest'
    //     },
    //     body: JSON.stringify({
    //       "options": this.state,
    //       "id": this.props.dashlet_id,
    //       "category": 'OpalUiGrid_Chart_Def',
    //     })
    //   })
    //     .then(res => {
    //       //toast.success("Guardado satisfactoriamente");
    //       return true;
    //     })
    //     .catch(error => {
    //       try {
    //         toast.error(error);
    //       } catch (err) {
    //         toast.error("Ocurrió un error al guardar");
    //       }
    //       return false;
    //     });
    //   await this.props.toggleChartsModal();
    //   this.props.setChartStruct(this.state);

    // } else {
    //   this.props.addChart(this.state);
    //   this.setState({ ...this.initial_state });
    // }
  }

  onHandleChange = (field, value) => {
    this.setState({
      [field]: value
    });
  }

  closeModal = async () => {
    this.setState({ ...this.initial_state }); 
    await this.props.setEditChartIndex(undefined);
    this.props.toggleChartsModal();
  }

  render() {
    const { show_charts_modal, groups, columns, edit_chart_index } = this.props;
    let axis_options = columns.map(field => { return { value: field.key_name, label: field.label, selectfield_params: field.selectfield_params } }).concat(groups.map(field => { return { value: field.key_name, label: field.label, selectfield_params: field.selectfield_params } }));

    let yaxis_props = {}

    if (['donut'].indexOf(this.state.type) >= 0) {
      yaxis_props["isMulti"] = true;
      yaxis_props["onChange"] = (items) => { this.onHandleChange('yaxis', (items ? items.map((item) => { return item.value }) : [])) };
      yaxis_props["value"] = axis_options.filter((data) => { return this.state.yaxis.indexOf(data.value) !== -1 });
    } else {
      yaxis_props["isMulti"] = false;
      yaxis_props["onChange"] = (items) => { this.onHandleChange('yaxis', (items ? [items.value] : [])) };
      yaxis_props["value"] = axis_options.filter((data) => { return this.state.yaxis.indexOf(data.value) !== -1 });
    }

    let yaxis_label = "Eje Y";

    switch (this.state.type) {
      case "donut":
        yaxis_label = "Valores";
        break;
      case "funnel":
      case "numeric":
        yaxis_label = "Valor";
        break;
      default:
        break;
    }

    return (<Modal scrollable={false} centered={true} size="lg" isOpen={show_charts_modal} toggle={this.closeModal} className="config-modal charts-modal">
      <ModalBody>
        <div className="chart-sidebar">
          <span className="chart-title">{typeof edit_chart_index !== 'undefined' && edit_chart_index !== null ? 'Edita' : 'Crea'} un Gráfico</span>
          <Form>
            <Row form>
              <Col>
                <FormGroup>
                  <Label for="name">Nombre</Label>
                  <Input type="text" name="name" id="name" className="input-sm" bsSize="sm" value={this.state.title} onChange={(e) => { this.onHandleChange("title", e.target.value); }} />
                </FormGroup>
              </Col>
              <Col>
                <FormGroup>
                  <Label for="chart_type">Seleccione un tipo de gráfico</Label>
                  <Select name="chart_type" options={charts_options} onChange={(e) => { this.onHandleChange("type", e.value); }} value={charts_options.find((e) => { return e.value === this.state.type })} />
                </FormGroup>
              </Col>
              {this.state.type !== 'numeric' ? <Col>
                <FormGroup>
                  <Label for="name">{this.state.type === 'donut' ? 'Etiqueta' : 'Eje X'}</Label>
                  <Select name="xaxis" options={axis_options} onChange={(e) => { this.onHandleChange("xaxis", e.value); this.getOptionsEnums(e.selectfield_params); }} value={axis_options.find((e) => { return e.value === this.state.xaxis })} />
                </FormGroup>
              </Col> : null}
              <Col>
                <FormGroup>
                  <Label for="name">{yaxis_label}</Label>
                  <Select name="yaxis" {...yaxis_props} options={axis_options} />
                </FormGroup>
              </Col>
              {['line', 'area', 'stacked_bar', 'grouped_bar', 'horizontal_stacked_bar', 'horizontal_grouped_bar'].indexOf(this.state.type) >= 0 ? <Col>
                <FormGroup>
                  <Label for="value_field">Valor</Label>
                  <Select name="value_field" options={axis_options} onChange={(e) => { this.onHandleChange("value_field", e.value); }} value={axis_options.find((e) => { return e.value === this.state.value_field })} />
                </FormGroup>
              </Col> : null}
              <Col>
                <FormGroup>
                  <Label for="name">Operador</Label>
                  <Select name="operation" options={operator_options} onChange={(e) => { this.onHandleChange("operator", e.value); }} value={operator_options.find((e) => { return e.value === this.state.operator })} />
                </FormGroup>
              </Col>
              {['bar', 'stacked_bar', 'grouped_bar', 'horizontal_bar', 'horizontal_stacked_bar', 'horizontal_grouped_bar', 'funnel'].indexOf(this.state.type) >= 0 && this.state.order_options.length > 0 ? <Col>
                <FormGroup>
                  <Label for="orden">Orden</Label>
                  <Select name="yaxis" isMulti options={this.state.order_options} onChange={(items) => {
                    this.onHandleChange('order_options_value', (items ? items : []))
                  }} value={this.state.order_options_value} />
                </FormGroup>
              </Col> : null}
            </Row>
            {this.state.type === 'numeric' ?
              <Row>
                <Col md={6}>
                  <FormGroup>
                    <Label for="name">Color del Texto</Label>
                    <ColorPicker defaultColor="#000" onChange={(color) => { this.onHandleChange("text_color", color); }} value={this.state.text_color} />
                  </FormGroup>
                </Col>
                <Col md={6}>
                  <FormGroup>
                    <Label for="name">Color del Fondo</Label>
                    <ColorPicker defaultColor="#fff" onChange={(color) => { this.onHandleChange("background_color", color); }} value={this.state.background_color} />
                  </FormGroup>
                </Col>
              </Row> : null}

          </Form>
        </div>
        <div className="chart-preview">
          <div className="chart-dx-preview">
            <span className="chart-preview-title">Vista Previa</span>
            {this.state.type ? <OpalChart is_preview={true} chart_def={{ ...this.state }} /> : <div className="dash-placeholder">
              <EmptyImage />
            </div>}

          </div>

          <ModalFooter>
            <Button color="primary" className="btn_primary_def_1" onClick={this.onSave}>{typeof edit_chart_index !== 'undefined' && edit_chart_index !== null ? 'Editar' : 'Agregar'}</Button>
            <Button color="secondary" className="btn_secondary_def_1"  onClick={() => { this.closeModal(); }}>Cancelar</Button>
          </ModalFooter>
        </div>

      </ModalBody>

    </Modal>);
  }
}


const mapStateToProps = state => {
  return {
    'show_charts_modal': state.general.show_charts_modal,
    'groups': state.general.groups,
    'columns': state.general.columns,
    //'is_create_edit': state.general.is_create_edit,
    'edit_chart_index': state.general.edit_chart_index,
    'dashlet_id': state.general.dashlet_id,
    'chart_list': state.general.chart_list,
  };
};

const mapDispatchToProps = dispatch => {
  return {
    'toggleChartsModal': () => {
      dispatch(toggleChartsModalAction());
    },
    'addChart': (chart_def) => {
      dispatch(addChart(chart_def));
    },
    'setEditChartIndex': (chart_index) => {
      dispatch(setEditChartIndex(chart_index));
    },
    'editChart': (index, chart_def) => {
      dispatch(editChart(index, chart_def));
    },
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(ChartsModal);