import cloneDeep from 'lodash/cloneDeep';
import { combineReducers } from 'redux';
import { 
  ADD_FIELD, 
  FETCH_PREVIEW_DATA_ERROR, 
  FETCH_PREVIEW_DATA_PENDING, 
  FETCH_PREVIEW_DATA_SUCCESS, 
  MOVE_FIELD, 
  REMOVE_FIELD, 
  SAVE_DATA_ERROR, 
  SAVE_DATA_PENDING, 
  SAVE_DATA_SUCCESS, 
  SET_DRAGGING_FIELD, 
  TOGGLE_CONFIG_MODAL, 
  TOGGLE_CHARTS_MODAL, 
  UPDATE_FIELD_LIST, 
  UPDATE_OBJECT_PROPERTY, 
  UPDATE_SECTION_ITEM, 
  ADD_CHART, 
  REMOVE_CHART, 
  APPLY_TABLE_FILTER, 
  SET_CHARTS_DATA, 
  ZOOM_CHART, 
  SET_CURRENT_VIEW, 
  SET_EDIT_CHART_INDEX, 
  SET_ORDER_COLUMNS, 
  EDIT_CHART, 
  SET_IS_OPEN_MODAL_FORMULA,
  SET_MODE_MODAL_FORMULA,
  SET_EDIT_MODE_DATA_MODAL_FORMULA,
  SET_INDEX_MODAL_FORMULA
} from './actions';

const onMoveField = (state, result) => {
  const { destination, source } = result;

  if (!destination) {
    return state;
  }

  if (
    destination.droppableId === source.droppableId &&
    destination.index === source.index
  ) {
    return state;
  }

  const newState = cloneDeep(state);
  const start = newState[source.droppableId];
  const finish = (source.droppableId === destination.droppableId ? start : newState[destination.droppableId]);
  const field_obj = start[source.index];

  start.splice(source.index, 1);
  finish.splice(destination.index, 0, field_obj);

  return newState;
}

const ReducerGeneral = (state = {}, action) => {
  let tmp_list = [];
  let new_state = {};
  switch (action.type) {
    case ADD_FIELD:
      tmp_list = [...state[action.section]];
      tmp_list.push(action.field);
      new_state = { ...state };
      new_state[action.section] = tmp_list;
      return new_state;

    case REMOVE_FIELD:
      tmp_list = [...state[action.section]];
      tmp_list.splice(action.index, 1)
      new_state = { ...state };
      new_state[action.section] = tmp_list;
      return new_state;

    case ADD_CHART:
      new_state = cloneDeep(state);
      new_state.chart_list.push(action.chart_def);
      return new_state;

    case REMOVE_CHART:
      tmp_list = [...state.chart_list];
      tmp_list.splice(action.index, 1)
      new_state = { ...state };
      new_state.chart_list = tmp_list;
      return new_state;

    case UPDATE_SECTION_ITEM:
      new_state = cloneDeep(state);
      new_state[action.section][action.item_index][action.property] = action.value;
      return new_state;

    case SET_DRAGGING_FIELD:
      return { ...state, dragging: action.dragging };

    case MOVE_FIELD:
      return onMoveField(state, action.result);
    case FETCH_PREVIEW_DATA_PENDING:
      return {
        ...state,
        loading: true,
        timeout_data_id: action.timeout_id
      }
    case FETCH_PREVIEW_DATA_SUCCESS:
      return {
        ...state,
        loading: false,
        preview_data: action.data
      }
    case FETCH_PREVIEW_DATA_ERROR:
      return {
        ...state,
        loading: false,
        error: action.error
      }
    case SAVE_DATA_PENDING:
      return {
        ...state,
        saving: true
      }
    case SAVE_DATA_SUCCESS:
      return {
        ...state,
        saving: false,
        id: action.data.id
      }
    case SAVE_DATA_ERROR:
      return {
        ...state,
        saving: false,
        error: action.error
      }
    case TOGGLE_CONFIG_MODAL:
      return {
        ...state,
        show_config_modal: !state.show_config_modal
      }
    case TOGGLE_CHARTS_MODAL:
      return {
        ...state,
        show_charts_modal: !state.show_charts_modal,
        dashlet_id: (action.dashlet_id ? action.dashlet_id : state.dashlet_id)
      }

    case UPDATE_OBJECT_PROPERTY:
      new_state = cloneDeep(state);
      new_state[action.object_name][action.property] = action.value;
      return new_state;

    case UPDATE_FIELD_LIST:
      new_state = { ...state };
      new_state.field_list = action.field_list;
      return new_state;

    case APPLY_TABLE_FILTER:
      tmp_list = cloneDeep(state.table_filters);
      let current_filter = tmp_list.find(item => {
        if (Array.isArray(item) && item.length > 0) {
          return item[0] === action.column;
        }
        return false;
      });

      if (current_filter && current_filter.length === 3) {
        current_filter[1] = "=";
        current_filter[2] = action.value;
      } else {
        tmp_list.push([action.column, "=", action.value]);
      }

      new_state = { ...state };
      new_state.table_filters = tmp_list;
      return new_state;


    case SET_CHARTS_DATA:
      new_state = { ...state };
      new_state.charts_data = action.data;
      return new_state;

    case ZOOM_CHART:
      new_state = { ...state };
      new_state.zoomed_chart_def = action.chart_def;
      return new_state;

    case SET_EDIT_CHART_INDEX:
      new_state = { ...state };
      new_state.edit_chart_index = action.chart_index;
      return new_state;

    case EDIT_CHART:
      tmp_list = [...state.chart_list];
      tmp_list[action.index] = action.chart_def;
      new_state = { ...state };
      new_state.chart_list = tmp_list;
      return new_state;
      
    case SET_CURRENT_VIEW:
      new_state = { ...state };
      new_state.current_view = action.current_view;
      new_state.is_create_edit = ['edit', 'create'].indexOf(action.current_view) !== -1;
      new_state.preview_data = [];
      new_state.table_filters = [];
      return new_state;

    case SET_ORDER_COLUMNS:
      return { ...state, columns: state.columns.map(e=> {
          let newE = {...e, ...{isSorter:false, sortType:null}};
          if(action.column.name === newE.name){
            return {...newE,...action.values}
          }
          return newE;
        })
      }
        
    default:
      return state;
  }
};

const reducerModalFormula = (state = {
  isOpen:false,
  mode: 'add',
  edit_mode_data: {},
  index: undefined,
}, action) => {
  const { type, payload } = action

  switch (type) {
    case SET_IS_OPEN_MODAL_FORMULA:
      return { ...state, isOpen: payload.isOpen }
    case SET_MODE_MODAL_FORMULA:
      return { ...state, mode: payload.mode }
    case SET_EDIT_MODE_DATA_MODAL_FORMULA:
      return { ...state, edit_mode_data: payload.edit_mode_data }
    case SET_INDEX_MODAL_FORMULA:
      return { ...state, index: payload.index }
    default:
      return state
  }
}

const reducers = combineReducers({
  'general': ReducerGeneral,
  'modalFormula': reducerModalFormula
});

export default reducers;