import React from 'react';

export default function CurrencyCell(cellData) {
  let data = cellData.value;
  if(data || data === 0){
      //data = data.toFixed(4);
      data = "$ " + data.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
  }
  return (
    <div>
      <div className="current-value">{data}</div>
    </div>
  );
}
