import React, { Component } from 'react';
import { connect } from 'react-redux';;

export class TableFooter extends Component {
  render() {
    const { data, is_create_edit } = this.props;
    let footer_total = (data ? data.length : 0);
    return (
      <div className="table-footer">
        {is_create_edit ?

          <span className="info-message"><i className="fa fa-info-circle" aria-hidden="true"></i> Está realizando una vista previa de un número limitado de registros. Guarde y cierre el informe para ver todo.</span>
          : <span className="info-message">{footer_total + " registros"}</span>}
      </div>
    );
  }
}

const mapStateToProps = (state) => ({
  'data': state.general.charts_data,
  'is_create_edit': state.general.is_create_edit,
});

export default connect(mapStateToProps)(TableFooter);
