import cloneDeep from 'lodash/cloneDeep';
import React, { Component } from 'react';
import { connect } from 'react-redux';
import Select from 'react-select';
import { Button, Col, Form, FormGroup, Input, Label, Modal, ModalBody, ModalFooter, ModalHeader, Row } from 'reactstrap';
import { fetchPreviewDataSuccess, toggleConfigModal as toggleConfigModalAction, updateObjectProperty as updateObjectPropertyAction } from '../storage/actions';
import { getSelectedGroupedOption } from '../Utils';
import SourceTree from './SourceTree';



export class ConfigModal extends Component {

    constructor(props) {
        super(props);
        this.onHandleChange = this.onHandleChange.bind(this);
    }

    async onHandleChange(property, value) {
        await this.props.updateObjectProperty("report_header_data", property, value);
    }

    onChangeSource = async (e) => {
        await this.onHandleChange('selected_class', e.value);
        await this.onHandleChange('module_path', e.class_path);
        this.props.cleanPreviewData();
    }
    render() {
        const { report_header_data, users_list, toggleConfigModal, show_config_modal, modules_list, roles_list, groups, filters, columns, is_create_edit, teams_list } = this.props;
        const module_class_list = cloneDeep(modules_list);
        module_class_list.map((item) => {
            if (!item.options) {
                return item;
            }
            item.options.map((x) => { x["value"] = x["class_name"]; return x });
            return item
        });
        return (<Modal scrollable={false} centered={true} size="lg" isOpen={show_config_modal} toggle={toggleConfigModal} className="config-modal">
            <ModalHeader toggle={toggleConfigModal}><i className="fa fa-cog" aria-hidden="true"></i> Configuración</ModalHeader>
            <ModalBody>
                <Form>
                    <Row form>
                        <Col md={12}>
                            <FormGroup>
                                <Label for="name">Nombre del reporte</Label>
                                <Input type="text" disabled={!is_create_edit} name="name" id="name" onChange={(e) => { this.onHandleChange("name", e.target.value); }} value={report_header_data.name} />
                            </FormGroup>
                        </Col>
                        {/* <Col md={6}>
                            <FormGroup>
                                <Label for="owner">Propietario</Label>
                                <Select name="owner" isDisabled={!is_create_edit} options={users_list} onChange={(e) => { this.onHandleChange('owner_id', e.value) }} value={users_list.find((e) => { return e.value === report_header_data.owner_id })} />
                            </FormGroup>
                        </Col> */}
                    </Row>
                    {/* <Row form>
                        <Col md={4}>
                            <FormGroup>
                                <Label for="visible_on_module">Mostrar en</Label>
                                <Select isDisabled={!is_create_edit} name="visible_on_module" options={modules_list} onChange={(e) => { this.onHandleChange('visible_on_module', e.value) }} value={getSelectedGroupedOption(modules_list, report_header_data.visible_on_module)} />
                            </FormGroup>
                        </Col>
                        <Col md={4}>
                            <FormGroup>
                                <Label for="visible_users">Visible para los usuarios</Label>
                                <Select isDisabled={!is_create_edit} isMulti name="visible_users" options={users_list} onChange={(items) => { this.onHandleChange('visible_users', (items ? items.map((item) => { return item.value }) : [])) }} value={users_list.filter((user) => { return report_header_data.visible_users.indexOf(user.value) !== -1 })} />
                            </FormGroup>
                        </Col>
                        <Col md={4}>
                            <FormGroup>
                                <Label for="visible_roles">Visible para los roles</Label>
                                <Select isDisabled={!is_create_edit} isMulti name="visible_roles" options={roles_list} onChange={(items) => { this.onHandleChange('visible_roles', (items ? items.map((item) => { return item.value }) : [])) }} value={roles_list.filter((rol) => { return report_header_data.visible_roles.indexOf(rol.value) !== -1 })} />
                            </FormGroup>
                        </Col>
                    </Row> */}
                    <Row form>
                        <Col md={12}>
                            <FormGroup>
                                <Label for="description">Descripción</Label>
                                <Input disabled={!is_create_edit} type="textarea" name="description" id="description" onChange={(e) => { this.onHandleChange("description", e.target.value); }} value={report_header_data.description} />
                            </FormGroup>
                        </Col>
                    </Row>
                    {/* <Row form>
                        <Col md={12}>
                            <FormGroup>
                                <Label for="owner_team">Equipo asignado</Label>
                                <Select isDisabled={!is_create_edit} name="owner_team_id" options={teams_list} onChange={(e) => { this.onHandleChange('owner_team_id', e.value) }} value={(report_header_data.owner_team_id && teams_list ? teams_list.find((team) => { return team.value === report_header_data.owner_team_id }) : null)} />
                            </FormGroup>
                        </Col>
                    </Row> */}
                    <Row form>
                        <Col md={12}>
                            <FormGroup>
                                <Label for="module_path">Fuente</Label>
                                <div className="source-tree">
                                    <Col md={6}>
                                        <Select isDisabled={(!is_create_edit || (groups && groups.length > 0) || (filters && filters.length > 0) || (columns && columns.length > 0))} name="module_path" options={module_class_list} onChange={(e) => { this.onChangeSource(e) }} value={getSelectedGroupedOption(module_class_list, report_header_data.selected_class)} />
                                    </Col>
                                    <Col className="tree-container" md={12}>
                                        <SourceTree />
                                    </Col>

                                </div>

                            </FormGroup>
                        </Col>
                    </Row>
                </Form>
            </ModalBody>
            <ModalFooter>
                {/* <Button color="secondary" onClick={toggleConfigModal}>Cancelar</Button> */}
                <Button color="primary" className="btn_primary_def_1" onClick={toggleConfigModal}>Continuar</Button>

            </ModalFooter>
        </Modal>);
    }
}


const mapStateToProps = state => {
    return {
        'report_header_data': state.general.report_header_data,
        'users_list': state.general.users_list,
        'show_config_modal': state.general.show_config_modal,
        'modules_list': state.general.modules_list,
        'roles_list': state.general.roles_list,
        'groups': state.general.groups,
        'filters': state.general.filters,
        'columns': state.general.columns,
        'is_create_edit': state.general.is_create_edit,
        'teams_list': state.general.teams_list
    };
};

const mapDispatchToProps = dispatch => {
    return {
        'toggleConfigModal': () => {
            dispatch(toggleConfigModalAction());
        },
        'updateObjectProperty': async (object_name, property, value) => {
            await dispatch(updateObjectPropertyAction(object_name, property, value));
        },
        'cleanPreviewData': () => {
            dispatch(fetchPreviewDataSuccess([]));
        },
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(ConfigModal);