import cloneDeep from 'lodash/cloneDeep';
import React, { Component } from 'react';
import { connect } from 'react-redux';
import FieldsContainer from './FieldsContainer';
import FormulaModal from '../FormulaModal';
import { setIsOpenModalFormula, setModeModalFormula } from '../../storage/actions'

export class Sidebar extends Component {

  constructor(props){
    super(props);
    this.state = {
      current_tab: 0,
      collapse: (props.is_create_edit ? false: true)
    }
  }

  changeTab(index) {
    this.setState({ current_tab: index });
  }

  onClickIcon() {
    let new_index = Math.abs(this.state.current_tab - 1);
    this.setState({ current_tab: new_index });
  }

  toggleCollapse = () => {
    this.setState(prevState => ({
      collapse: !prevState.collapse
    }));
  }

  openEditFormula = () => {
    this.props.dispatchSetModeModalFormula('add')
    this.props.dispatchSetIsOpen(true)
  }

  filterFieldList = (excluded_keys) => {
    let result = [];
    this.props.field_list.forEach(element => {
      let tmp = cloneDeep(element);
      if (tmp.options) {
        let new_options = [];
        for (let index = 0; index < tmp.options.length; index++) {
          const option = tmp.options[index];
          if (excluded_keys.indexOf(option.key_name) === -1) {
            new_options.push(option);
          }
        }
        tmp.options = new_options;
        result.push(tmp);
      }
    });
    return result;
  }

  render() {
    const { columns, groups, filters, is_create_edit } = this.props;
    let added_fields = columns.map(field => { return field.key_name }).concat(groups.map(field => { return field.key_name }));
    if(this.state.collapse){
      return (
        <div className="oreports-sidebar collapsed">
          <div className="sidebar-header">
            <div className="sidebar-icon" onClick={this.toggleCollapse}><i className="fa fa-chevron-right" aria-hidden="true"></i></div>
          </div>
        </div>
      );
    }
    return (
      <div className="oreports-sidebar">
        <div className="sidebar-header">
          {this.state.current_tab === (is_create_edit ? 0 : 1) ? <div className="sidebar-title"><i className="fa fa-th" aria-hidden="true"></i> Tabla base</div> : null}
          <div className="sidebar-icon" onClick={() => { this.onClickIcon(); }}>{this.state.current_tab === (is_create_edit ? 0 : 1) ? <i className="fa fa-filter" aria-hidden="true"></i> : <i className="fa fa-th" aria-hidden="true"></i>}</div>
          {this.state.current_tab === (is_create_edit ? 1 : 0) ? <div className="sidebar-title"><i className="fa fa-filter" aria-hidden="true"></i> Filtros</div> : null}
          <div className="sidebar-collapse" onClick={this.toggleCollapse}><i className="fa fa-chevron-left" aria-hidden="true"></i></div>
        </div>
        <div className="padding-15">
          {this.state.current_tab === (is_create_edit ? 0 : 1) ? <>
            <FormulaModal />
            <FieldsContainer title="Columnas" section="columns" fields={columns} field_list={this.filterFieldList(added_fields)}
            titleButton={(is_create_edit ? {classes: 'btn btn-default oreports-primary-button outlined custom-columns-function', click: this.openEditFormula, classIcon: 'fa fa-columns' } : null)} />
            <hr />
            <FieldsContainer title="Grupos" section="groups" fields={groups} field_list={this.filterFieldList(added_fields)} />
          </> : <FieldsContainer title="Filtros" notitle={true} section="filters" fields={filters} field_list={this.filterFieldList([])} />}
        </div>
      </div>
    );
  }
}

const mapStateToProps = state => {
  return {
    'field_list': state.general.field_list,
    'columns': state.general.columns,
    'groups': state.general.groups,
    'filters': state.general.filters,
    'is_create_edit': state.general.is_create_edit
  };
};

const mapDispatchToProps = dispatch => {
	return {
    dispatchSetIsOpen: isOpen => dispatch(setIsOpenModalFormula(isOpen)),
    dispatchSetModeModalFormula: mode => dispatch(setModeModalFormula(mode))
	}
}


export default connect(mapStateToProps, mapDispatchToProps)(Sidebar);
