const dateAdd = {
	'name': 'dateAdd',
	'label': 'Sumar a fecha',
	'title': 'dateAdd',
	'description': 'Adiciona a una fecha. el último argumento, "unidad", puede ser uno de los siguientes: "years", "quarters", "months", "weeks", "days", "hours", "minutes", "seconds" o "miliseconds"',
	'syntax': 'dateAdd(props(campo_fecha_hora), cantidad, unidad)',
	'examples': [
		"dateAdd(props(campo_fecha_hora), 7, 'years') -> dateAdd('2021-01-12T11:50:14-05:00', 7, 'years') = '2028-01-12T11:50:14-05:00'",
		"dateAdd(props(campo_fecha_hora), 2, 'quarters') -> dateAdd('2021-01-12T11:50:58-05:00', 2, 'quarters') = '2021-10-12T11:50:58-05:00'",
		"dateAdd(props(campo_fecha_hora), 2, 'months') -> dateAdd('2021-01-12T11:51:22-05:00', 2, 'months') = '2021-03-12T11:51:22-05:00'",
		"dateAdd(props(campo_fecha_hora), 3, 'weeks') -> dateAdd('2021-01-12T11:51:22-05:00', 3, 'weeks') = '2021-02-02T11:51:22-05:00'",
		"dateAdd(props(campo_fecha_hora), 4, 'days') -> dateAdd('2021-01-12T11:51:22-05:00', 4, 'days') = '2021-01-16T11:51:22-05:00'",
		"dateAdd(props(campo_fecha_hora), 8, 'hours') -> dateAdd('2021-01-12T11:51:22-05:00', 8, 'hours') = '2021-01-16T19:51:22-05:00'",
		"dateAdd(props(campo_fecha_hora), 1, 'minutes') -> dateAdd('2021-01-12T11:51:22-05:00', 1, 'minutes') = '2021-01-16T11:52:22-05:00'",
		"dateAdd(props(campo_fecha_hora), 1, 'seconds') -> dateAdd('2021-01-12T11:51:22-05:00', 1, 'seconds') = '2021-01-16T11:51:23-05:00'",
		"dateAdd(props(campo_fecha_hora), 5, 'milliseconds') -> dateAdd('2021-01-12T11:51:22-05:00', 1000, 'milliseconds') = '2021-01-16T11:51:23-05:00'",
	],
	'printFormula': () => 'dateAdd('
}

