import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Col } from 'reactstrap';
import { Button } from 'devextreme-react/button';

import OpalChart from './Chart';

import { removeChart, zoomChart, applyTableFilter, setEditChartIndex, toggleChartsModal } from '../storage/actions';


class ChartContainer extends Component {

  render() {

    const { chart_def, chart_index, is_create_edit, size, table_filters } = this.props;

    let show_undo = false;
    let undo_field = null;
    for (let index = 0; index < table_filters.length; index++) {
      const element = table_filters[index];
      if ((element[0] === chart_def.xaxis || (chart_def.yaxis  && chart_def.yaxis.length > 0 && element[0] === chart_def.yaxis[0])) && element.length === 3 && element[2]) {
        show_undo = true;
        undo_field = (element[0] === chart_def.xaxis ? chart_def.xaxis: chart_def.yaxis[0]);
        break;
      }

    }

    return (
      <Col md={size} className="chart-card" style={{
        padding: chart_def.type === "numeric" ? 0 : 'inherit'
      }}>
        <OpalChart chart_def={{ ...chart_def }} />
        <div className="chart-button-container">
          {chart_def.type !== "numeric" ? <Button
            icon="search"
            onClick={() => {
              this.props.zoomChart({ ...chart_def });
            }} /> : null}
          {show_undo ?
            <Button
              icon="undo"
              onClick={() => {
                this.props.applyFilter(undo_field, undefined);
              }}
            /> : null}
          {is_create_edit ?
          <>
          <Button
            icon="edit"
            onClick={() => {
              this.props.editChart(chart_index);
            }} />
            <Button
              icon="close"
              onClick={() => {
                this.props.removeChart(chart_index);
              }}
            /> </>: null}
        </div>
      </Col>);
  }
}


const mapStateToProps = state => {
  return {
    'is_create_edit': state.general.is_create_edit,
    'table_filters': state.general.table_filters,
  };
};

const mapDispatchToProps = dispatch => {
  return {
    'removeChart': (index) => {
      dispatch(removeChart(index));
    },
    'editChart': async (chart_index) => {
      await dispatch(setEditChartIndex(chart_index));
      dispatch(toggleChartsModal());
    },
    'zoomChart': (chart_def) => {
      dispatch(zoomChart(chart_def));
    },
    'applyFilter': (column, value) => {
      dispatch(applyTableFilter(column, value));
    }
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(ChartContainer);