import React from 'react';

export default function LinkCell(cellData) {
  let field_link = "";
  try {
    let field = cellData.column.dataField;
    let field_module = cellData.column.related_module_name;
    let field_id = field + "_link_id";
    field_link = "/" + field_module + "/detail/" + cellData.data[field_id] + "/";
  } catch (error) {
    field_link = "#";
  }
  let data = cellData.value;
  if (cellData.column.dataType === 'datetime' || cellData.column.dataType === 'date'){
    data = cellData.text;
  }
  return (
    <div>
      <a href={field_link} target="_blank" rel="noopener noreferrer">{data}</a>
      <div className="current-value"></div>
    </div>
  );
}
