import React, { Component } from 'react'
import { Chart } from 'devextreme-react/chart';

import PieChart from 'devextreme-react/pie-chart';
import { toast } from 'react-toastify';
import { connect } from 'react-redux';

import { applyTableFilter } from '../storage/actions';
import isEqual from 'lodash/isEqual';
import { numberFormat } from '../Utils';
import Funnel, {
  Item,
  Border,
  Label,
  Font,
  Legend
} from 'devextreme-react/funnel';
import _ from 'lodash';


//TODO: Obtener desde el backend
const colors_scheme = ["#80c9a7", "#bada8c", "#326fac", "#32b9b0", "#56bd8d", "#d1e5b1", "#ffd690", "#5ac5d9", "#6691c0", "#00a89d", "#addfea", "#f27c93", "#84d1e0", "#134b95", "#a8d9c4", "#a3cf66", "#f9a94b", "#f9bca5", "#69c7c2", "#ffe3b5", "#8cc640", "#f5a6b7", "#f9be77", "#f05a26", "#ed294e", "#f69c7b", "#2fb6d0", "#f79420", "#ef5370", "#f37a50", "#ffca6d", "#9bd7d5", "#1eb473", "#fcbb48", "#fcd4a4", "#98b7d5"]


class OpalChart extends Component {

  constructor(props) {
    super(props);
    this.state = {
      data: [],
      line_series: []
    }
  }

  groupData = async (props, replaces) => {
    // if (props.loading) {
    //   return;
    // }
    let data = props.data;
    let chart_def = props.chart_def;
    if (data.length <= 0 || (props.is_preview && data.length > 300 && !chart_def.operator)) {
      return this.setState({ data: [], line_series: [] });
    }

    if (typeof replaces === 'undefined') {
      replaces = {};
    }

    let acum_dict = {};
    let xaxis = chart_def.xaxis;
    if (chart_def.type === 'numeric') {
       xaxis = "oreports_all";
    }
    let operator = chart_def.operator;
    if (!chart_def.operator) {
      return this.setState({ data: data, line_series: [] });
    }

    let xaxis_oreports_key = xaxis + "_oreports_key";
    let type_with_value = ['line', 'area', 'stacked_bar', 'grouped_bar', 'horizontal_stacked_bar', 'horizontal_grouped_bar'].indexOf(chart_def.type) !== -1;
    let use_value_field = (type_with_value && chart_def.value_field ? true : false);
    let line_series = [];

    await chart_def.yaxis.forEach(yaxis => {

      for (let i = 0; i < data.length; i++) {
        try {
          const row = data[i];
          const xaxis_argument = (row[xaxis] in replaces ? replaces[row[xaxis]] : row[xaxis]);
          const xaxis_argument_oreports_key = (row[xaxis_oreports_key] in replaces ? replaces[row[xaxis_oreports_key]] : row[xaxis_oreports_key]);
          const value = (use_value_field ? Number(row[chart_def.value_field]) : Number(row[yaxis]));
          if (!(xaxis_argument in acum_dict)) {
            acum_dict[xaxis_argument] = {};
            acum_dict[xaxis_argument][xaxis] = xaxis_argument;
            acum_dict[xaxis_argument][xaxis_oreports_key] = xaxis_argument_oreports_key;

            var colorIndex = i;
            if (colorIndex > colors_scheme.length - 1) {
              colorIndex = colorIndex % colors_scheme.length;
            }
            acum_dict[xaxis_argument]["color"] = colors_scheme[colorIndex];
          }

          let yaxis_agurment = (use_value_field ? row[yaxis] : yaxis);
          if (use_value_field && line_series.indexOf(yaxis_agurment) === -1) {
            line_series.push(yaxis_agurment);
          }

          if(chart_def.type === 'numeric' && operator === 'count' && row[yaxis] !== null && line_series.indexOf(row[yaxis]) === -1){
            line_series.push(row[yaxis]);
          }

          if (!(yaxis_agurment in acum_dict[xaxis_argument])) {
            acum_dict[xaxis_argument][yaxis_agurment + "__counter"] = 1;
            if (operator === 'avg') {
              acum_dict[xaxis_argument][yaxis_agurment] = [value];
            } else if (operator === 'count') {
              acum_dict[xaxis_argument][yaxis_agurment] = 1;
            } else {
              acum_dict[xaxis_argument][yaxis_agurment] = value;
            }
          } else {
            acum_dict[xaxis_argument][yaxis_agurment + "__counter"] += 1;
            if (operator === 'sum') {
              acum_dict[xaxis_argument][yaxis_agurment] += value;
            } else if (operator === 'count') {
              acum_dict[xaxis_argument][yaxis_agurment] += 1;
            } else if (operator === 'min') {
              acum_dict[xaxis_argument][yaxis_agurment] = Math.min(acum_dict[xaxis_argument][yaxis_agurment], value);
            } else if (operator === 'max') {
              acum_dict[xaxis_argument][yaxis_agurment] = Math.max(acum_dict[xaxis_argument][yaxis_agurment], value);
            } else if (operator === 'avg') {
              acum_dict[xaxis_argument][yaxis_agurment].push(value);
            }
          }
        } catch (error) {
          console.log("error for", error);
        }

      }
      if (operator === 'avg') {
        for (var row_key in acum_dict) {
          if (use_value_field) {
            for (var attr_key in acum_dict[row_key]) {
              if (acum_dict[row_key].hasOwnProperty(attr_key) && Array.isArray(acum_dict[row_key][attr_key])) {
                acum_dict[row_key][attr_key] = acum_dict[row_key][attr_key].reduce(function (a, b) {
                  return a + b;
                }, 0) / acum_dict[row_key][attr_key].length;
              }
            }
          } else {
            acum_dict[row_key][yaxis] = acum_dict[row_key][yaxis].reduce(function (a, b) {
              return a + b;
            }, 0) / acum_dict[row_key][yaxis].length;
          }
        }
      }else if(chart_def.type === 'numeric' && operator === 'count'){
        
        acum_dict[undefined][yaxis] = line_series.length;
        line_series = [];
      }
    });
    let return_data = Object.values(acum_dict);
    if (chart_def && chart_def.order_options_value && chart_def.order_options_value.length > 0 && return_data.length > 0) {
      const options = chart_def.order_options_value.map(e => { return e.value });
      return_data = return_data.filter(row => {
        return options.indexOf(row[xaxis_oreports_key]) !== -1;
      });
      return_data.sort((row1, row2) => {
        const index1 = options.indexOf(row1[xaxis_oreports_key]);
        const index2 = options.indexOf(row2[xaxis_oreports_key]);
        return ((index1 > -1 ? index1 : Infinity) - (index2 > -1 ? index2 : Infinity));
      });
    }


    if (return_data.length > 300) {
      toast.error("Muchos datos para graficar, pruebe cambiando los filtros.");
      return this.setState({ data: [], line_series: line_series });
    }


    return this.setState({ data: return_data, line_series });
  }

  componentWillMount() {
    if (this.props.data.length > 0) {
      this.groupData(this.props);
    }
  }

  componentWillReceiveProps(new_props) {
    if ((!isEqual(new_props.data, this.props.data)) || (new_props.chart_def.xaxis !== this.props.chart_def.xaxis) || (new_props.chart_def.value_field !== this.props.chart_def.value_field) || (!isEqual(new_props.chart_def.yaxis, this.props.chart_def.yaxis)) || (new_props.chart_def.type !== this.props.chart_def.type) || (new_props.chart_def.operator !== this.props.chart_def.operator) || (new_props.loading !== this.props.loading) || (!isEqual(new_props.chart_def.order_options_value, this.props.chart_def.order_options_value))) {
      this.groupData(new_props);
    }
  }

  onPointClick = (info, agurment_field = false) => {
    if (info.item && agurment_field) {
      this.props.applyFilter(agurment_field, info.item.argument);
    }
    if (info.target && info.target.series) {
      this.props.applyFilter(info.target.series.getArgumentField(), info.target.argument);
    }
  }

  onLegendClick = (argument) => {
    return (info) => {
      if (argument && info.target) {

        let value = info.target;

        if (value.name) {
          value = value.name;
        }
        if (['line', 'area', 'stacked_bar', 'grouped_bar', 'horizontal_stacked_bar', 'horizontal_grouped_bar'].indexOf(this.props.chart_def.type) !== -1 && this.props.chart_def.yaxis && this.props.chart_def.yaxis.length > 0) {
          this.props.applyFilter(this.props.chart_def.yaxis[0], value);
        } else {
          this.props.applyFilter(argument, value);
        }
      }
    }
  }

  compareValues = (key, order = 'asc') => {
    return function innerSort(a,b){
      if(!a.hasOwnProperty(key) || !b.hasOwnProperty(key))return 0;

      const varA = (typeof a[key] === 'string') ? a[key].toUpperCase() : a[key];
      const varB = (typeof b[key] === 'string') ? b[key].toUpperCase() : b[key];

      let comparision = 0;
      if(varA > varB){
        comparision = 1;
      }else if(varA < varB){
        comparision = -1;
      }

      return ((order === 'desc') ? (comparision * -1) : comparision)
    }

  }

  render() {

    const { chart_def, groups, columns, isZoomed } = this.props;

    // if (loading) {
    //   return <div className="chart-loading"><span><i className="fa fa-spinner fa-spin" aria-hidden="true"></i> Cargando...</span></div>;
    // }
    let axis_options = columns.map(field => { return { value: field.key_name, label: field.label, field_type: field["type"] } }).concat(groups.map(field => { return { value: field.key_name, label: field.label, field_type: field["type"] } }));
    let chart_type = chart_def.type;
    let is_line_type = ['line', 'area', 'stacked_bar', 'grouped_bar', 'horizontal_stacked_bar', 'horizontal_grouped_bar'].indexOf(chart_def.type) !== -1;

    let chart_props = {
      title: chart_def.title,
      dataSource: this.state.data,
      tooltip: {
        enabled: true,
        format: (value) => {
          return numberFormat(value, 2);
        },
      },
      legend: {
        visible: true
      },
      onPointClick: this.onPointClick,
      onLegendClick: this.onLegendClick(chart_def.xaxis)
    }

    switch (chart_def.type) {
      case "stacked_bar":
        chart_type = "stackedBar";
        break;
      case "grouped_bar":
        chart_type = "bar";
        break;
      case "horizontal_bar":
        chart_type = "bar";
        chart_props["rotated"] = true;
        break;
      case "horizontal_stacked_bar":
        chart_type = "stackedBar";
        chart_props["rotated"] = true;
        break;
      case "horizontal_grouped_bar":
        chart_type = "bar";
        chart_props["rotated"] = true;
        break;
      default:
        break;
    }
    let series = [];

    if (is_line_type) {
      series = this.state.line_series.map(element => {
        return {
          valueField: element,
          argumentField: chart_def.xaxis,
          type: chart_type,
          ignoreEmptyPoints: false,
          name: element
        }
      });
    } else {
      series = chart_def.yaxis.map(element => {
        let yaxis_option = axis_options.find(item => { return item.value === element });
        return {
          valueField: element,
          argumentField: chart_def.xaxis,
          type: chart_type,
          ignoreEmptyPoints: true,
          name: (yaxis_option ? yaxis_option.label : '')
        }
      });
    }


    chart_props["tooltip"] = {
      enabled: true,
      format: (value) => {
        return numberFormat(value, 2);
      },
      customizeTooltip: function (data) {
        let large_number = numberFormat(data.originalValue, 2, true);
        var txt = data.argumentText;
        if (data.argumentText !== data.seriesName) {
          txt += "\n" + data.seriesName;
        }
        txt += "\n" + large_number;
        return { text: txt }
      }
    }



    let chart_component = null;
    if (chart_type === "donut") {
      chart_props["commonSeriesSettings"] = {
        label: {
          visible: true,
          font: {
            size: 16,
            color: "black"
          },
          connector: {
            visible: true,
            width: 0.5
          },
        },
        argumentField: chart_def.xaxis,
        type: ''
      };
      chart_props["tooltip"] = {
        enabled: true,
        format: (value) => {
          return numberFormat(value, 2);
        },
        customizeTooltip: function (arg) {
          let large_number = numberFormat(arg.originalValue, 2, true);
          return {
            text: `${arg.argumentText}\n${large_number} - ${(arg.percent * 100).toFixed(2)}%`
          };
        }
      }
      chart_props["legend"] = {
        margin: 0,
        horizontalAlignment: "right",
        verticalAlignment: "top"
      }
      series.forEach(serie => {
        serie.label = {
          format: (value) => {
            return numberFormat(value, 2);
          },
        }
      });
      chart_props["series"] = series;
      chart_props["resolveLabelOverlapping"] = "hide";

      chart_component = (
        <PieChart
          {...chart_props}
          ref={(c) => this.chartRef = c}
        >
        </PieChart>
      );
    } else if (chart_type === "funnel") {
      chart_props["argumentField"] = chart_def.xaxis;
      chart_props["valueField"] = (series.length > 0 ? series[0].valueField : '');
      chart_props["tooltip"] = {
        enabled: true,
        format: (value) => {
          return numberFormat(value, 2);
        },
        customizeTooltip: function (data) {
          let large_number = numberFormat(data.value, 2, true);
          var txt = `${data.item.argument}\n${large_number}`;
          return { text: txt }
        }
      }
      chart_props["onItemClick"] = (info) => {
        return this.onPointClick(info, chart_def.xaxis);
      }

      chart_component = (
        <Funnel
          {...chart_props}
          ref={(c) => this.chartRef = c}
          sortData={(chart_def.order_options_value && chart_def.order_options_value.length > 0 ? false : true)}
          algorithm={"dynamicHeight"}
        >
          <Legend visible={isZoomed ? true : false} />
          <Item>
            <Border visible={true} />
          </Item>
          <Label
            visible={isZoomed ? true : false}
            horizontalAlignment="left"
            backgroundColor="none"
            customizeText={(arg) => {
              let formated_number = numberFormat(arg.value, 2);
              return `${arg.item.argument} - ${formated_number}`;
            }}
          ><Font size={16} color={"#000"} /></Label>
        </Funnel>
      );
    } else if (chart_type === "numeric") {
      let chart_value = 0;
      let value_field = series.length > 0 ? series[0].valueField : '';
      (this.state.data || []).forEach(element => {
        chart_value += element[value_field] || 0;
      });

      let value_type = axis_options.find((data) => { return data.value === value_field });
      let large_number = 0;
      if (value_type) {
        switch (value_type.field_type) {
          case "CurrencyField":
            large_number = "$" + numberFormat(chart_value, 2, true);
            chart_value = "$" + numberFormat(chart_value, 2);
            break;
          default:
            large_number = numberFormat(chart_value, 2, true);
            chart_value = numberFormat(chart_value, 2);
            break;
        }
      }
      chart_component = (
        <div className="numeric-chart" title={large_number} style={{
          backgroundColor: chart_def.background_color || 'inherit',
          color: chart_def.text_color || 'inherit',
        }}>
          <span className="numeric-chart-title">{chart_def.title || ''}</span>
          <span className="numeric-chart-body">{chart_value}</span>
        </div>
      );
    } else {
      if (series.length > 1 || is_line_type) {
        chart_props["series"] = series;
      } else {
        chart_props["seriesTemplate"] = {
          nameField: chart_def.xaxis
        };

        chart_props["commonSeriesSettings"] = {
          argumentField: chart_def.xaxis,
          valueField: (series.length > 0 ? series[0].valueField : ''),
          type: chart_type,
          ignoreEmptyPoints: true,
        };
      }
      
      const type_value_x = axis_options.find(e=>e.value === chart_def.xaxis)
      if(type_value_x && type_value_x.field_type === "IntegerField")chart_props['argumentAxis'] = {'type':'discrete'}

      const colSorter = columns.find(e=> e.isSorter);
      if(colSorter){
        const sortType = colSorter.sortType || null ? colSorter.sortType : 'desc';
        let dataSorted = _.cloneDeep(chart_props.dataSource);
        chart_props['dataSource'] = dataSorted.sort(this.compareValues(colSorter.name, sortType));
      }
      
      chart_component = (<Chart 
        ref={(c) => this.chartRef = c}
        {...chart_props}
      >
      </Chart>);
    }
    return chart_component;
  }
}

const mapStateToProps = state => {
  return {
    'groups': state.general.groups,
    'columns': state.general.columns,
    'is_create_edit': state.general.is_create_edit,
    //'loading': state.general.loading,
    'data': state.general.charts_data,
  };
};

const mapDispatchToProps = dispatch => {
  return {
    'applyFilter': (column, value) => {
      dispatch(applyTableFilter(column, value));
    }
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(OpalChart);
