import Tree, { TreeNode } from 'rc-tree';
import 'rc-tree/assets/index.css';
import React, { Component } from 'react';
import cookie from 'react-cookies';
import { connect } from 'react-redux';
import { toast } from 'react-toastify';
import { updateFieldList, updateObjectProperty as updateObjectPropertyAction } from '../storage/actions';
import { formatFieldParameter } from '../Utils';
import cloneDeep from 'lodash/cloneDeep'

export class SourceTree extends Component {

  constructor(props) {
    super(props);
    this.state = this.getInitialStateBaseSource(props, true);
  }

  getInitialStateBaseSource = (props, initial) => {
    if (initial) {
      if (props.report_header_data && props.report_header_data.metadata && props.report_header_data.metadata.treeData && props.report_header_data.metadata.treeData.length > 0 && props.report_header_data.metadata.checkedKeys && props.report_header_data.metadata.checkedKeys.checked.length > 0) {
        return {
          treeData: props.report_header_data.metadata.treeData,
          checkedKeys: props.report_header_data.metadata.checkedKeys,
          expandedKeys: props.report_header_data.metadata.expandedKeys
        }
      }
    }
    let selected_module_info = null;
    let treeData = [];
    let checkedKeys = {
      checked: [],
      halfChecked: []
    };
    let expandedKeys = [];
    for (let index = 0; index < props.modules_list.length; index++) {
      const element = props.modules_list[index];
      if (element.options && element.options.length > 0) {
        let value = element.options.find((e) => { return e.class_name === props.report_header_data.selected_class });
        if (value) {
          selected_module_info = value;
          selected_module_info.path = selected_module_info.class_name;
          treeData.push(selected_module_info);
          if (checkedKeys.checked.indexOf(selected_module_info.path) === -1) {
            checkedKeys.checked.push(selected_module_info.path);
          }
          expandedKeys = checkedKeys.checked;
          break;
        }
      }
    }

    return {
      treeData,
      checkedKeys,
      expandedKeys
    }
  }

  async componentWillReceiveProps(new_props) {
    if ((new_props.report_header_data && this.props.report_header_data) && (new_props.report_header_data.selected_class !== this.props.report_header_data.selected_class)) {
      await this.setState(this.getInitialStateBaseSource(new_props));
      this.onCheck(this.state.checkedKeys);
    }
  }

  componentDidMount() {
    this.onCheck(this.state.checkedKeys, true);
  }

  getFieldList = async (element, get_new = false) => {
    let node = this.findPathInTree(this.state.treeData, element);
    if (!node) {
      return null;
    }

    if (!node.field_list || get_new) {
      await this.loadBranch(node).catch((error) => { console.log(error); node.field_list = [] });
    }
    return {
      label: node.breadcrumbs_label || node.label,
      options: node.field_list
    };

  }

  onCheck = async (checkedKeys, get_new = false) => {
    let fields = [];
    let keys_list = [];

    if (get_new && checkedKeys && checkedKeys.checked) {
      checkedKeys.checked.forEach(e => {
        let element_list = [];
        while (e.lastIndexOf("::") > -1) {
          element_list.push(e);
          e = e.substr(0, e.lastIndexOf("::"));
        }
        element_list.forEach(el => {
          if (keys_list.indexOf(el) === -1) {
            keys_list.push(el);
          }
        });
      });

      if(keys_list.length > 0){
        keys_list.push(keys_list[0].split("::")[0]);
      }
      keys_list = keys_list.sort();

      if(keys_list.length === 0){
        keys_list.push(this.props.report_header_data.selected_class);
      }

    } else {
      keys_list = checkedKeys.checked;
    }

    for (let index = 0; index < keys_list.length; index++) {
      const element = keys_list[index];
      let element_fields = await this.getFieldList(element, get_new);
      if (element_fields && checkedKeys.checked.indexOf(element) > -1) {
        fields.push(element_fields);
      }
    }
    this.setState({
      checkedKeys,
    });

    this.props.updateFieldList(fields);
  }


  onExpand = (expandedKeys) => {
    this.setState({
      expandedKeys,
    });
  }

  findPathInTree = (tree, path, parent) => {
    for (let index = 0; index < tree.length; index++) {
      const element = tree[index];
      element.breadcrumbs_label = (parent ? parent.breadcrumbs_label + " > " : '') + element.label;
      if (element.path === path) {
        return element;
      }

      if (element.children && element.children.length > 0) {
        let tmp = this.findPathInTree(element.children, path, element);
        if (tmp) {
          return tmp;
        }
      }
    }
    return null;
  }

  onClickLoad = async (treeNode,b,c,d,e) => {

    //const wait = time => new Promise(resolve => setTimeout(resolve, time))

    const treeData = [...this.state.treeData];
    let node = this.findPathInTree(treeData, treeNode.props.eventKey);
    let labelNode = node.label
    node.label += " cargando..."
    node.fetchingData = true
    this.setState({ treeData })
    //await wait(3000)
    await this.loadBranch(node).finally(() => { 
      node.label = labelNode
      delete node.fetchingData
    })
    this.setState({ treeData })
    const expandedKeys = [...this.state.expandedKeys]
    expandedKeys.push(treeNode.props.eventKey);
    this.setState({ treeData, expandedKeys });
  }

  componentWillUnmount() {
    if (this.props.is_create_edit) {
      this.props.updateObjectProperty('report_header_data', 'metadata', this.state);
    }

  }

  loadBranch = async (node) => {
    node.children = [];

    return fetch('/oreports/get_class_info/', {
      method: 'POST',
      credentials: "same-origin",
      headers: {
        "Accept": "application/json",
        "X-CSRFToken": cookie.load('csrftoken'),
        'X-Requested-With': 'XMLHttpRequest'
      },
      body: JSON.stringify(node)
    }).then(res => res.json())
      .then(data => {
        if (data.error) {
          throw (data.error);
        }
        node.children = [];
        let relationships = data['relationships'];
        node.field_list = data['fields'] || [];

        for (let i = 0; i < node.field_list.length; i++) {
          const field = node.field_list[i];
          field.path = node.path;
          field.key_name = formatFieldParameter(node.path, field.name);
        }

        relationships['many_to_many'].forEach((element, index) => {
          element.path = node.path + '::' + element.fk_name;
          node.children.push(element);
        });

        relationships['many_to_one'].forEach((element, index) => {
          element.path = node.path + '::' + element.fk_name;
          node.children.push(element);
        });

        relationships['one_to_many'].forEach((element, index) => {
          element.path = node.path + '::' + element.class_name;
          node.children.push(element);
        });


      }).catch(error => {
        console.log(error);
        try {
          toast.error(error);
        } catch (err) {
          toast.error("Ocurrió un error al guardar");
        }
      });
  }

  render() {
    let first_node_disabled = false;
    const { is_create_edit } = this.props;
    const loop = (data, parent=undefined) => {
      return data.map((item, index) => {
        let disabled = (first_node_disabled === false && index === 0 ? true : false);
        first_node_disabled = true;
        let showChildren = true
        let content

        if (parent && item.label.startsWith('(m2m)') && item.label === parent.label) {
          showChildren = false
        }

        if (showChildren) {
          content = item.children ? loop(item.children, item) : null
        } else {
          content = null
        }

        let disableNode = false

        if ('fetchingData' in item && item.fetchingData) {
          disableNode = true
        }

        return (
          <TreeNode disabled={ disableNode } disableCheckbox={ disabled || !is_create_edit } title={item.label} key={item.path} isLeaf={false} module_info={item}>{ content }</TreeNode>
        );
      });
    };
    const treeNodes = loop(this.state.treeData);
    return (
      <div >
        <Tree
          checkable
          showIcon={false}
          selectable={false}
          onCheck={this.onCheck}
          checkedKeys={this.state.checkedKeys}
          expandedKeys={this.state.expandedKeys}
          onExpand={this.onExpand}
          autoExpandParent={true}
          loadData={this.onClickLoad}
          checkStrictly
        >
          {treeNodes}
        </Tree>
      </div>
    )
  }
}

const mapStateToProps = state => {
  return {
    'report_header_data': state.general.report_header_data,
    'modules_list': cloneDeep(state.general.modules_list),
    'is_create_edit': state.general.is_create_edit
  };
};

const mapDispatchToProps = dispatch => {
  return {
    'updateObjectProperty': (object_name, property, value) => {
      dispatch(updateObjectPropertyAction(object_name, property, value));
    },
    'updateFieldList': (field_list) => {
      dispatch(updateFieldList(field_list));
    }
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(SourceTree)
