import React, { Component } from 'react';
import { Draggable } from "react-beautiful-dnd";
import { PopoverBody, UncontrolledPopover } from 'reactstrap';
import FieldOptionsColumns from './FieldOptions/FieldOptionsColumns';
import FieldOptionsFilters from './FieldOptions/FieldOptionsFilters';
import FieldOptionsGroups from './FieldOptions/FieldOptionsGroups';

const modifiers = {
  preventOverflow: {
    enabled: false,
  },
  hide: {
    enabled: false
  },
  flip: {
    enabled: false,
  },
};

export class DraggableField extends Component {

  render() {
    const { field, onRemove, index, onHandleChange, section, isDragDisabled, isCreateEdit } = this.props;
    let fld_options = null;
    switch (section) {
      case "groups":
        fld_options = <FieldOptionsGroups field={field} onHandleChange={onHandleChange} index={index} section={section} isCreateEdit={isCreateEdit} />;
        break;
      case "filters":
        fld_options = <FieldOptionsFilters field={field} onHandleChange={onHandleChange} index={index} section={section} isCreateEdit={isCreateEdit} />;
        break;

      default:
        fld_options = <FieldOptionsColumns field={field} onHandleChange={onHandleChange} index={index} section={section} isCreateEdit={isCreateEdit} />;
        break;
    }
    return (
      <Draggable draggableId={field.key_name + "_" + index} index={index} isDragDisabled={isDragDisabled}>
        {(provided, snapshot) => {
          return (
            <div  {...provided.draggableProps}
              {...provided.dragHandleProps}
              ref={provided.innerRef}
              className="draggable-field-container"
            >
              <div id={"Popover-" + field.key_name + "_" + index} className="draggable-field">
                <span className="draggable-field-label">{field.label}</span>
                {isCreateEdit ? <i className="fa fa-times-circle" aria-hidden="true" onClick={onRemove}></i> : null}
              </div>
              <UncontrolledPopover
                placement="right-start"
                target={"Popover-" + field.key_name + "_" + index}
                trigger="legacy"
                modifiers={modifiers}
                delay={0}
                fade={false}
              >
                <PopoverBody className={"oreports-popover"}>
                  {fld_options}
                </PopoverBody>
              </UncontrolledPopover>
            </div>);
        }}

      </Draggable>
    );
  }
}

export default DraggableField;