import React, { Component } from 'react';
import { Droppable } from "react-beautiful-dnd";
import { connect } from 'react-redux';
import Select from 'react-select';
import { Label, Button } from 'reactstrap';
import { addField, removeField, updateSectionItem } from '../../storage/actions';
import { fetchPreviewData as fetchPreviewDataAction } from '../../storage/thunkActions';
import DraggableField from './DraggableField';

let Container = (props) => {
  const { innerref, children } = props;
  const cProps = { ...props };

  let className = props.className;
  // if (props.dragging) {
  //   className += " monitor-drop-in-container";
  // }

  // if (props.snapshot.isDraggingOver) {
  //   className += " monitor-current-drop-container";
  // }
  const bannedProps = ['innerref', 'dragging', 'dispatch', 'snapshot'];
  for (let i of bannedProps) {
    delete cProps[i];
  }
  return (
    <div ref={innerref} {...cProps} className={className}>
      {children}
    </div>
  );
}

export class FieldsContainer extends Component {
  state = {
    selected_option: ''
  }
  render() {
    const { title, notitle, field_list, section, fields, is_create_edit, titleButton } = this.props;
    return (
      <div className="fields-main-container">
        <div className="fields-title-container">
          {!notitle ? <div className="fields-title-label-container"><Label size="15">{title}</Label></div> : null}
          {titleButton ? <div className="fields-title-action-container"><Button className={titleButton.classes} onClick={ titleButton.click } size="sm"><i className={titleButton.classIcon} aria-hidden="true"></i></Button></div> : null}
        </div>
          {is_create_edit ? <Select placeholder="Seleccione un campo" options={field_list} onChange={(e) => { this.props.addField(section, e); }} value={this.state.selected_option} /> : null}
        <Droppable droppableId={section}>
          {(provided, snapshot) => (

            <Container className={"fields-container" + (this.props.dragging ? " box-bordered" : "")}
              innerref={provided.innerRef}
              {...provided.droppableProps}
              snapshot={snapshot}
            >
              {fields.map((field, index) => (
                <DraggableField isCreateEdit={is_create_edit} isDragDisabled={!is_create_edit} section={section} field={field} index={index} key={field.key_name + "_" + index} onRemove={() => { this.props.removeField(section, index) }} onHandleChange={(property, value) => { this.props.updateSectionItem(section, index, property, value) }} />
              ))}
              {provided.placeholder}
            </Container>
          )}
        </Droppable>

      </div>
    )
  }
}

const mapStateToProps = state => {
  return {
    'dragging': state.general.dragging,
    'is_create_edit': state.general.is_create_edit
  };
};


const mapDispatchToProps = dispatch => {
  return {
    'addField': async (section, field) => {
      await dispatch(addField(section, field));
      dispatch(fetchPreviewDataAction());
    },
    'removeField': async (section, index) => {
      await dispatch(removeField(section, index));
      dispatch(fetchPreviewDataAction());
    },
    'updateSectionItem': async (section, index, property, value) => {
      await dispatch(updateSectionItem(section, index, property, value));
      dispatch(fetchPreviewDataAction());
    }
  };
};


export default connect(mapStateToProps, mapDispatchToProps)(FieldsContainer);
