import 'devextreme/dist/css/dx.common.css';
import 'devextreme/dist/css/dx.light.css';
import 'react-toastify/dist/ReactToastify.css';
//import 'bootstrap/dist/css/bootstrap.css';
import './index.scss';

import React, { Component } from 'react';
import { DragDropContext } from "react-beautiful-dnd";
import { connect } from 'react-redux';
import { Col, Container, Row, Modal, ModalBody, ModalHeader, Button } from 'reactstrap';
import ConfigModal from './components/ConfigModal';
import ChartsModal from './components/ChartsModal';
import ZoomChartModal from './components/ZoomChartModal';
import ChartContainer from './components/ChartContainer';
import EmptyState from './components/EmptyState';
import Sidebar from './components/Sidebar/Sidebar';
import Table from './components/Table';
import './sass/mixins.scss';
import { moveField, setDraggingField, toggleConfigModal, toggleChartsModal, setCurrentView } from './storage/actions';
import { fetchPreviewData as fetchPreviewDataAction, save as saveAction } from './storage/thunkActions';
import Dashlet from './components/Dashlet';



export class App extends Component {
  state = {
    show_modal_dashlet: false
  }

  componentDidMount() {
    const { fetchPreviewData } = this.props;
    fetchPreviewData();

    window["oreports_app_" + this.props.app_id] = this;
  }

  toggleZoomDashlet = async () => {
    if (this.props.is_create_edit) {
      await this.props.setCurrentView('detail');
      this.props.fetchPreviewData();
    }
    this.setState(prevState => ({
      show_modal_dashlet: !prevState.show_modal_dashlet
    }));
  }

  render() {
    const { chart_list, is_ajax, ajax_show_all } = this.props;

    let charts = null;
    if (chart_list && chart_list.length > 0) {
      let size = Math.floor(12 / chart_list.length);
      charts = chart_list.map((chart_item, index) => {
        return (<ChartContainer key={index} size={size < 4 ? 4 : size} chart_index={index} chart_def={chart_item} />);
      });
    }

    if (is_ajax) {
      this.zoomChart = () => {
        this.toggleZoomDashlet();
      }

      this.editFromDashlet = async (dashlet_id) => {
        await this.props.setCurrentView('edit');
        this.props.fetchPreviewData();
        this.setState(prevState => ({
          show_modal_dashlet: !prevState.show_modal_dashlet
        }));
      }
    }

    const render_component = (
      <div>
        <ConfigModal />
        <ChartsModal />
        <ZoomChartModal />
        <DragDropContext onDragEnd={this.props.moveField} onDragStart={this.props.startDragging}>
          <Container fluid={true}>
            {charts && charts.length > 0 ?
              <Row className="row-chart-container">
                <div className="chart-container">
                  {charts}
                </div>
              </Row> : null}
            <Row>
              <Col>
                <div className="oreports-container">

                  <div className="oreports-body">
                    <Sidebar />
                    <div className="oreports-table">
                      {this.props.columns && this.props.columns.length > 0 ?
                        <Table />
                        : <EmptyState />}
                    </div>
                  </div>
                </div>
              </Col>
            </Row>
          </Container>
        </DragDropContext>
      </div>
    );

    if (is_ajax) {
      const dashlet_title = (this.props.detail_name  ? ' - ' + this.props.detail_name: '');
      let modal_header = (<ModalHeader toggle={this.toggleZoomDashlet}><i className="fa fa-cog" aria-hidden="true"></i> Detalle{dashlet_title}</ModalHeader>);
      if (this.props.is_create_edit) {
        modal_header = (
          <ModalHeader close={<></>} toggle={this.toggleZoomDashlet}>
            <div className="dashlet-modal-header">
              <div className="self-center"><i className="fa fa-cog" aria-hidden="true"></i> Edición{dashlet_title}</div>
              <div className="modal-header-buttons">
                <Button className="btn-icon" onClick={() => { this.props.toggleChartsModal() }}><i className="fa fa-pie-chart" aria-hidden="true"></i></Button>
                <Button color="primary" className="oreports-primary-button" onClick={() => { this.props.save() }}>Guardar</Button>
                <Button color="primary" onClick={() => { this.toggleZoomDashlet() }}>Cerrar</Button>
              </div>
            </div>
          </ModalHeader>);
      }
      return (<div>
        {(!ajax_show_all && chart_list && chart_list.length > 0) ? <Dashlet chart_def={chart_list[0]} /> : render_component}
        <Modal scrollable={false} centered={true} size="lg" isOpen={this.state.show_modal_dashlet} toggle={this.toggleZoomDashlet} className="root_oreports config-modal full-screen-modal">
          {modal_header}
          <ModalBody>
            {render_component}
          </ModalBody>
        </Modal>
      </div>);
    }

    return render_component;
  }
}

const mapStateToProps = state => {
  return {
    'columns': state.general.columns,
    'saving': state.general.saving,
    'chart_list': state.general.chart_list,
    'is_ajax': state.general.is_ajax,
    'ajax_show_all': state.general.ajax_show_all,
    'app_id': state.general.app_id,
    'is_create_edit': state.general.is_create_edit,
    'detail_name': state.general.detail_name,
  };
};

const mapDispatchToProps = dispatch => {
  return {
    'moveField': async (result) => {
      dispatch(setDraggingField(false));
      dispatch(moveField(result));
      //dispatch(fetchPreviewDataAction());
    },
    'startDragging': () => {
      dispatch(setDraggingField(true));
    },
    'fetchPreviewData': () => {
      dispatch(fetchPreviewDataAction());
    },
    'save': (close_window = false) => {
      dispatch(saveAction(close_window));
    },
    'toggleConfigModal': () => {
      dispatch(toggleConfigModal());
    },
    'toggleChartsModal': (dashlet_id) => {
      dispatch(toggleChartsModal(dashlet_id));
    },
    'setCurrentView': (current_view) => {
      dispatch(setCurrentView(current_view));
    }
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(App);