import React, { Component } from 'react';
import OpalChart from './Chart';
import Table from './Table';

class Dashlet extends Component {
  render() {
    return (
      <div className="oreport-dashlet">
        <OpalChart chart_def={this.props.chart_def} />
        <Table hidden={true} />
      </div>
    )
  }
}

export default Dashlet;
