import cookie from 'react-cookies';
import { toast } from 'react-toastify';
import { fetchPreviewDataError, fetchPreviewDataPending, fetchPreviewDataSuccess, saveDataError, saveDataPending, saveDataSuccess } from './actions';

import {
    formulasLib
} from '../Utils'

export const save = (close_window = false) => {
    return (dispatch, getState) => {
        let current_state = getState().general || {};
        dispatch(saveDataPending());
        fetch('/oreports/save/', {
            method: 'POST',
            credentials: "same-origin",
            headers: {
                "Accept": "application/json",
                "X-CSRFToken": cookie.load('csrftoken'),
                'X-Requested-With': 'XMLHttpRequest'
            },
            body: JSON.stringify({
                "id": current_state.id,
                "name": current_state.report_header_data.name,
                "description": current_state.report_header_data.description,
                "selected_class": current_state.report_header_data.selected_class,
                "visible_users": current_state.report_header_data.visible_users,
                "visible_roles": current_state.report_header_data.visible_roles,
                "visible_on_module": current_state.report_header_data.visible_on_module,
                "module_path": current_state.report_header_data.module_path,
                "select_list": current_state.columns,
                "filter_list": current_state.filters,
                "chart_list": current_state.chart_list,
                "owner": { id: current_state.report_header_data.owner_id },
                "owner_team": { id: current_state.report_header_data.owner_team_id },
                "group_list": current_state.groups,
                "version": 2,
                "metadata": JSON.stringify( Object.assign(current_state.report_header_data.metadata || {}, {
                    field_list: current_state.field_list,
                }))
            })
        })
            .then(res => res.json())
            .then(res => {
                if (res.error) {
                    throw (res.error);
                }
                toast.success("Guardado satisfactoriamente");
                dispatch(saveDataSuccess(res));
                if (close_window) {
                    window.location.href = '/oreports/detail/' + res.id;
                }
                return res;
            })
            .catch(error => {
                try {
                    toast.error(error);
                } catch (err) {
                    toast.error("Ocurrió un error al guardar");
                }

                dispatch(saveDataError(error));
            })
    };
}

export const fetchPreviewData = () => {
    return (dispatch, getState) => {
        let current_state = getState().general || {};
        if (current_state.timeout_data_id !== null && typeof (current_state.timeout_data_id) !== "undefined") {
            clearTimeout(current_state.timeout_data_id);
        }
        let url = '';
        let params = {};
        if(current_state.is_create_edit){
            url = '/entry_point/oreports_preview_data/';
            //extraer las opciones del field_list en forma segura
            const regularColumns = current_state.columns.filter(col => !col.customFn)
            //concatenar las columnas 
            params = {
                "module_path": current_state.report_header_data.module_path,
                "selected_class": current_state.report_header_data.selected_class,
                "filters": current_state.filters,
                //"columns": current_state.columns,
                "columns": regularColumns,
                "groups": current_state.groups
            }
        }else{
            url = '/entry_point/oreports_update_data/';
            params = {
                "id": current_state.report_header_data.id,
                "filter_list": current_state.filters,
                'current_record': (current_state.current_record ? current_state.current_record : null)
            }
        }
        let timeoutData = setTimeout(() => {
            fetch(url, {
                method: 'POST',
                credentials: "same-origin",
                headers: {
                    "Accept": "application/json",
                    "X-CSRFToken": cookie.load('csrftoken'),
                    'X-Requested-With': 'XMLHttpRequest'
                },
                body: JSON.stringify(params)
            })
                .then(res => res.json())
                .then(res => {
                    const fieldsMapTypes = current_state.field_list[0].options.map(option => Object.assign({}, {[option.name]: option.type})).reduce((obj, curr) => {
                        return {...obj, ...curr}
                    }, {})
                    const formulaColumns = current_state.columns.filter(col => col.customFn).concat(current_state.groups.filter(col => col.customFn)) 
                    const calculatedColumnsNames = formulaColumns.map(col => col.key_name)
                    const calculatedColumnsFormulas = formulaColumns
                                                        .map(col => Object.assign({}, { [col.key_name]: col.formula }))
                                                        .reduce((obj, curr) => {
                                                            return {...obj, ...curr}
                                                        }, {})

                    let contextAsScope = item => () => { 
                        return {
                            ...formulasLib,
                            ...item
                        }
                    }
                    //columna tipo customFn, es necesario reemplazar los operandos en la formula y evaluar
                    if (res.error) {
                        throw (res.error);
                    }

                    const normalizeFormula = (formula) => {
                        return formula.replace(/props\((.*?)\)/g, function (not_use, operand) {
                            if (calculatedColumnsNames.indexOf(operand) === -1) { 
                                if ((operand in fieldsMapTypes) && ["DecimalField", "FloatField"].indexOf(fieldsMapTypes[operand]) !== -1) {
                                    return `(parseFloat(${operand} || 0))`
                                }  else {
                                    return `(${operand})`
                                }
                            } else {
                                return `(${normalizeFormula(calculatedColumnsFormulas[operand], true)})`
                            }
                        })
                    }

                    const parseRes = res.map(item => {

                        const calculatedColumns = formulaColumns.map(col => {
                            const itemContext = contextAsScope(item)()
                            return { [col.key_name]: (new Function(...Object.keys(itemContext), `return ${normalizeFormula(col.formula)}`))(...Object.values(itemContext)) }
                        }).reduce((obj, curr) => {
                            return {...obj, ...curr}
                        }, {})

                        return {...item, ...calculatedColumns }
                    });

                    dispatch(fetchPreviewDataSuccess(parseRes));
                    return parseRes
                })
                .catch(error => {
                    dispatch(fetchPreviewDataError(error));
                })
        }, 500);
        dispatch(fetchPreviewDataPending(timeoutData));
    }
}