import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Col, Modal, ModalBody, Row } from 'reactstrap';
import { zoomChart } from '../storage/actions';

import Table from '../components/Table';
import Sidebar from '../components/Sidebar/Sidebar';

import OpalChart from './Chart';


class ZoomChartModal extends Component {

  render() {

    const { zoomed_chart_def, toggleZoomChartModal } = this.props;

    if (!zoomed_chart_def || !zoomed_chart_def.type) {
      return null;
    }

    return (<Modal scrollable={false} centered={true} size="lg" isOpen={true} toggle={toggleZoomChartModal} className="config-modal full-screen-modal modal-chart">
      <ModalBody>
        <OpalChart chart_def={{ ...zoomed_chart_def }} isZoomed={true} />
        <div className="chart-button-container">
          <i className="fa fa-times" aria-hidden="true" onClick={toggleZoomChartModal}></i>
        </div>

        {this.props.show_table ?

          <Row>
            <Col>
              <div className="oreports-container">
                <div className="oreports-body">
                  <Sidebar />
                  <div className="oreports-table">
                    <Table in_modal={true} />
                  </div>
                </div>
              </div>
            </Col>
          </Row>
          : null}
      </ModalBody>

    </Modal>);
  }
}


const mapStateToProps = state => {
  return {
    'zoomed_chart_def': state.general.zoomed_chart_def,
  };
};

const mapDispatchToProps = dispatch => {
  return {
    'toggleZoomChartModal': () => {
      dispatch(zoomChart({}));
    },
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(ZoomChartModal);