import React, { Component } from 'react'
import { connect } from 'react-redux'
import { toast } from 'react-toastify'
import "ace-builds";
import "ace-builds/src-noconflict/theme-monokai";
import "ace-builds/src-noconflict/ext-language_tools";
import AceEditor from "react-ace";
import { CustomMode, setAutocomplete } from "./Formula/CustomAceBehaveaurt";
import Select from 'react-select'
import { cloneDeep } from 'lodash'

import {
	Alert,
	Button, 
	Col,
	FormGroup,
	Input,
	Label,
	Modal,
	ModalHeader,
	ModalBody,
	ModalFooter,
	Row,
	Badge
} from 'reactstrap'

import { 
	setIsOpenModalFormula, 
	addField,
	updateSectionItem
} from '../storage/actions'

import { fetchPreviewData } from '../storage/thunkActions'

import { 
	isEmptyObj, 
	reduceToObj,
	generateSample,
	getType,
	buildMeta,
	formulasLib,
	generateName
 } from '../Utils'

import {
	formulaDescriptions,
	emptyDesciptionObj,
	resetStateFormulaModal
} from '../constants'

class FormulaModal extends Component {

	constructor(props) {
		super(props)
		this.state = {
			'label': '',
			'name': '',
			'formula': '',
			'description': cloneDeep(emptyDesciptionObj),
			'section': 'columns',
			'formulaHelper': false,
			'formulaDescriptions': formulaDescriptions
		}
		this.onHandleChange = this.onHandleChange.bind(this)
		this.addToFormula = this.addToFormula.bind(this)
		this.onAddField = this.onAddField.bind(this)
		this.onHandleChangeFn = this.onHandleChangeFn.bind(this)
		this.isOpen = false;
	}

	componentWillReceiveProps(nextProps) {
		if (nextProps.modalFormulaMode === 'edit')  {
			this.setState(cloneDeep(nextProps.modalFormulaEditModeData))
		} else {
			this.setState({
				'label': '',
				'name': '',
				'formula': '',
				'description': cloneDeep(emptyDesciptionObj),
				'section': 'columns'
			})
		}
	}

	onHandleChange(e) {
		const { target: { name, value } } = e
		this.setState({ ...this.state, ...{ [name]: value } })
	}
	
	onHandleChangeEditor(e) {
		this.setState({ ...this.state, ...{ ["formula"]: e } })
	}

	onHandleChangeFn(fnOption) {
		if (isEmptyObj(fnOption)) {
			this.setState({...this.state, ...{ description: cloneDeep(emptyDesciptionObj) } })
			return
		}

		this.setState(
			{
				...this.state,
				...{ description: cloneDeep(fnOption) }
			}
		)
	}

	addToFormula(field) {
		if (!field) return
		const { name } = field
		this.setState({ ...this.state, ...{ formula: `${this.state.formula} props(${name}) ` } })
	}

	onAddField(e) {

		if (!this.state.label) {
			toast.error('Por favor ingrese un nombre a la columna')
			return
		}

		const key_name = generateName(this.state.label)

		const columnNames = this.props.columns.map(col => col.key_name).concat(this.props.groups.map(col => col.key_name))

		if (this.props.modalFormulaMode !== 'edit' && columnNames.indexOf(key_name) !== -1)
		{
			toast.error(`Ya existe llave columna con el valor ${key_name}`)
			return
		}

		if (!this.state.formula) {
			toast.error('Por favor ingrese una fórmula')
			return
		}

		let formulaBuildError = {
			'error': false,
			'description': ''
		}

		const formulaColumns = this.props.columns.filter(col => col.customFn).concat(this.props.groups.filter(col => col.customFn))
		const regularColumns = this.props.columns.filter(col => !col.customFn).concat(this.props.groups.filter(col => !col.customFn))

		const regularColumnsNames = regularColumns.map(col => col.key_name)
		const calculatedColumnsNames = formulaColumns.map(col => col.key_name)
		const calculatedColumnsFormulas = formulaColumns
											.map(col => Object.assign({}, { [col.key_name]: col.formula }))
											.reduce(reduceToObj, {})

		const normalizeFormula = function (formula, regularColumnsNames, calculatedColumnsNames, calculatedColumnsFormulas) {
			return formula.replace(/props\((.*?)\)/g, function (not_use, operand) {
				if (calculatedColumnsNames.indexOf(operand) === -1) {

					if(regularColumnsNames.indexOf(operand) !== -1) {
						const typeOperand = regularColumns.find(col => col.key_name === operand).type
						const sample = generateSample(typeOperand)
						if (typeOperand === 'CharField' || typeOperand === 'DateTimeField' || typeOperand === 'DateField')  {
							return `'${sample}'`
						} else {
							return sample
						}
					}

					formulaBuildError.error = true
					formulaBuildError.description = `El operando ${operand} no pertenece a la lista de columnas seleccionadas`
					return ''
				} else {
					return `(${normalizeFormula(calculatedColumnsFormulas[operand], regularColumnsNames, calculatedColumnsNames, calculatedColumnsFormulas)})`
				}
			})
		}

		const normFormula = normalizeFormula(this.state.formula, regularColumnsNames, calculatedColumnsNames, calculatedColumnsFormulas)

		if (formulaBuildError.error) {
			toast.error(`Formula invalida: ${normFormula}`)
			return
		}

		//Evaluar la formula
		let resultFormula = undefined
		try {
			resultFormula = (
				new Function(...Object.keys(formulasLib), `try {
					return ${normFormula}
				} catch (err)  {
					return err
				}`)
			)(...Object.values(formulasLib))
		} catch (err)  {
			toast.error('Se produjo un error al evaluar la fórmula')
			return
		}

		if (resultFormula instanceof Error) {
			toast.error(resultFormula.message)
			return
		}

		let resultType

		if (normFormula.indexOf('changeFormat') !== -1) {
			resultType = 'CharField'
		} else {
			resultType = getType(resultFormula)
		}

		if (!resultType) {
			//no permitir cerrar el modal, el resultado no es string ni number ni date ni boolean
			toast.error(`Se obtuvo un tipo inesperado. datos: ${normFormula}`)
			return
		}

		const metaType = buildMeta(resultType)

		if (this.props.modalFormulaMode === 'edit') {
			this.props.dispatchUpdateSectionItem(this.state.section, this.props.index, 'formula', this.state.formula)
			this.props.dispatchUpdateSectionItem(this.state.section, this.props.index, 'label', this.state.label)
			this.props.dispatchUpdateSectionItem(this.state.section, this.props.index, 'type', metaType.type)
			this.props.dispatchUpdateSectionItem(this.state.section, this.props.index, 'filter_options', { ...metaType.filter_options })
		} else {
			const fieldMeta = {
				"name": key_name,
				"label": this.state.label,
				...metaType,
				"path": this.props.selected_class,
				"key_name": key_name,
				"customFn": true,
				"formula": this.state.formula
			}
			this.props.dispatchAddField('columns', fieldMeta)
		}

		this.props.dispatchFetchPreviewData()
		this.setState(cloneDeep(resetStateFormulaModal))
		this.props.dispatchSetIsOpen(false)
	}

	detectFunctions (e) {
		try {
			const selection = e;
			const cursorPosition = {column:selection.cursor.column, row:selection.cursor.row};
			const textBeforeCursor = selection.doc.$lines[cursorPosition.row].slice(0, cursorPosition.column+1);
			if (textBeforeCursor.slice(-1) === '(') {
				const lastName = textBeforeCursor.slice(0, textBeforeCursor.length - 1)
				.split(/\(|\)|--|\+\+|===|==|=|!=|!==|<=|>=|<<=|>>=|>>>=|<>|<|>|!|&&|\|\||\?\:|[!$%&*+\-~\/^]=?/)
				.pop().replaceAll(' ', '');
				formulaDescriptions.forEach(fnOption => {
					if (fnOption.name === lastName) {
						this.setState({
							...this.state,
							...{ description: cloneDeep(fnOption) }
						});
					}
				} );
			} else {
				if (!this.state.formulaHelper) {
					this.setState({
						...this.state,
						...{ description: cloneDeep(emptyDesciptionObj) }
					});
				}
			}
		} catch (error)  {
			console.log("detectFunctions", error)
		}
	}

	render() {
		return <Modal
				scrollable={false}
				centered={true}
				size="lg"
				isOpen={ this.props.modalFormulaisOpen }
				toggle={ () => this.props.dispatchSetIsOpen(false) }
				className="config-modal">
					<ModalHeader
						toggle={ () => this.props.dispatchSetIsOpen(false) }>
						Fórmula
					</ModalHeader>
					<ModalBody>
						<div className="formula-modal-edit-container">
						<div className="formula-modal-row">
							<Col>
								<FormGroup>
									<Label for="label">Nombre Columna</Label>
									<Input
										type="text"
										name="label"
										id="label"
										className="input-sm"
										bsSize="sm"
										value={ this.state.label }
										onChange={ e => this.onHandleChange(e) } />
								</FormGroup>
							</Col>
						</div>
						<div className="formula-modal-row">
							<Col>
								<FormGroup>
									<Label for="name">
										Fórmula
										<Badge color="secondary" className={!this.state.formulaHelper ? "formula-edit-help": "active-option-button"}
											onClick={ async (e) => { this.setState({
												...this.state,
												...{ formulaHelper: !this.state.formulaHelper }
											});} }>
											<i className="fa fa-question-circle" aria-hidden="true"></i>
										</Badge>
									</Label>
									<AceEditor
										ref="aceEditor"
										placeholder="year(props(creation_date)) ..."
										mode="text"
										theme="monokai"
										name="formula"
										onLoad={editor => {setTimeout(() => {
											const customMode = new CustomMode();
											editor.session.setMode(customMode);
											setAutocomplete(formulaDescriptions, this.props.columns.concat(this.props.groups));
										}, 100);}}
										onChange={ e => {this.onHandleChangeEditor(e);}  }
										onCursorChange = { e => this.detectFunctions(e) }
										value={ this.state.formula }
										fontSize={14}
										setOptions={{
											enableBasicAutocompletion: true,
											showLineNumbers: true,
    										enableLiveAutocompletion: true
										}}
										showPrintMargin={true}
										showGutter={true}
										highlightActiveLine={true}
										style={{width: "100%", height: "100px"}}
										/>
										{(this.state.description && this.state.description.label !== '') || this.state.formulaHelper ?
								<ul className="formula-helper-container">
									<li className="formula-helper">
									<div style={{display:'flex'}}>
										<div className="helper-option-list" style={{width: '30%', marginRight: '5%'}}>
												<h5>Funciones</h5>
												<ul>
													{ formulaDescriptions.map((option, index) => (
														<li style={{listStyle: 'none'}}  key={index}>
															<button color="secondary" 
																className={!(this.state.description.name === option.name) ? "helper-funtion-option" : "helper-funtion-option active-option-button"}
																onClick={() => this.onHandleChangeFn(option)}>{option.label}
															</button>
														</li>
													)) }
												</ul>
										</div>
										{(this.state.description && this.state.description.label !== '') ?
										<div className="helper-option-detail" style={{width: '65%'}}>
											<h4>{ this.state.description.label } <span style={{fontSize: '12px', fontFamily: 'Avenir-Book,Arial,Helvetica,sans-serif'}}>(<i>{this.state.description.name}</i>)</span></h4>
											<p>
												{ this.state.description.description }
											</p>
											<h5>
												{ this.state.description.syntax && 'Sintax' }
											</h5>
											{ this.state.description.syntax && <Alert color="dark"><i>{ this.state.description.syntax }</i></Alert> }
											<h5>
												{ !!this.state.description.examples.length && 'Ejemplos' }
											</h5>
											{ this.state.description.examples.map((example, index) => (
												<Alert key={ index } color="dark">
													<i>{ example }</i>
												</Alert>
											)) }
										</div> : null}
										</div>
									</li>
								</ul> : null
							}
								</FormGroup>
							</Col>
						</div>
						</div>
					</ModalBody>
					<ModalFooter>
						<Button color="primary" className="btn_primary_def_1" onClick={ this.onAddField }>{ this.props.modalFormulaMode === 'edit' ? 'Aceptar' : 'Agregar' }</Button>
						<Button color="secondary" className="btn_secondary_def_1" onClick={ e => {
							this.props.dispatchSetIsOpen(false)
							this.setState(cloneDeep(resetStateFormulaModal))
						}}>Cancelar</Button>
					</ModalFooter>
				</Modal>
	}
}

const mapStateToProps = ({ modalFormula, general }) => {
	return {
		modalFormulaisOpen: modalFormula.isOpen,
		modalFormulaMode: modalFormula.mode,
		modalFormulaEditModeData: modalFormula.edit_mode_data,
		selected_class: general.report_header_data.selected_class,
		columns: general.columns,
		groups: general.groups,
		index: modalFormula.index,
	}
}

const mapDispatchToProps = dispatch => {
	return {
		dispatchSetIsOpen: isOpen => dispatch(setIsOpenModalFormula(isOpen)),
		dispatchAddField: (section, field) => dispatch(addField(section, field)),
		dispatchFetchPreviewData: () => dispatch(fetchPreviewData()),
		dispatchUpdateSectionItem: (section, index, property, value) => {
			dispatch(updateSectionItem(section, index, property, value))
		}
	}
}

export default connect(
	mapStateToProps,
	mapDispatchToProps
)(FormulaModal)