import React, { Component } from 'react';
import { connect } from 'react-redux'
import { setIsOpenModalFormula, setModeModalFormula, setIndexModalFormula, setEditModeDataModalFormula } from '../../../storage/actions'
import { Form, FormGroup, Input, Label, Badge } from 'reactstrap';

import {
	cloneDeep
} from 'lodash'

import {
	emptyDesciptionObj
} from '../../../constants'

export class FieldOptions extends Component {
    render() {
        const { onHandleChange, field, isCreateEdit } = this.props;
        return (<Form>
            <FormGroup>
                <Label for="module">Módulo</Label>
                <Input className="input-sm" bsSize="sm" type="text" name="module" disabled value={field.path} />
            </FormGroup>
            <FormGroup>
                <Label for="name">Campo</Label>
                <Input className="input-sm" bsSize="sm" type="text" name="name" disabled value={field.name} />
            </FormGroup>
            { field.customFn ? <FormGroup>
                <Label for="name">Fórmula</Label>{isCreateEdit ? <Badge color="secondary" className="edit-function-bagged-popover" onClick={ async (e) => {
                    this.props.dispatchSetModeModalFormula('edit')
                    this.props.dispatchSetIndexModalFormula(this.props.index)
                    this.props.dispatchSetEditModeDataModalFormula({
                        label: field.label,
                        name: field.name,
                        formula: field.formula,
                        description: cloneDeep(emptyDesciptionObj),
                        section: 'groups',
                    })
                    document.getElementsByClassName("popover show bs-popover-auto")[0].parentNode.classList.toggle("hidden")
                    this.props.dispatchSetIsOpen(true)
                } }><i className="fa fa-edit" aria-hidden="true"></i></Badge> : null }
                <Input className="input-sm" bsSize="sm" type="text" name="name" disabled value={field.formula} />
            </FormGroup> : null }
            <FormGroup>
                <Label for="label">Etiqueta</Label>
                <Input disabled={!isCreateEdit} className="input-sm" bsSize="sm" type="text" name="label" onChange={(e) => { onHandleChange("label", e.target.value); }} value={field.label} />
            </FormGroup>
            {/* <FormGroup check inline>
                <Label check>
                    Enlace {' '}
                    <Input type="checkbox" name="link" onChange={(e) => { onHandleChange("link", (e.target.checked ? 'True' : 'False')); }} checked={((field.link && typeof field.link === "boolean") || field.link === 'True') ? true : false} />
                </Label>
            </FormGroup> */}
        </Form>);
    }
}

const mapDispatchToProps = dispatch => {
	return {
        dispatchSetIsOpen: isOpen => dispatch(setIsOpenModalFormula(isOpen)),
        dispatchSetModeModalFormula: mode => dispatch(setModeModalFormula(mode)),
        dispatchSetIndexModalFormula: index => dispatch(setIndexModalFormula(index)),
        dispatchSetEditModeDataModalFormula: data => dispatch(setEditModeDataModalFormula(data))
	}
}

export default connect(undefined, mapDispatchToProps)(FieldOptions);