export const ADD_FIELD = 'ADD_FIELD';
export const REMOVE_FIELD = 'REMOVE_FIELD';
export const SET_DRAGGING_FIELD = 'SET_DRAGGING_FIELD';
export const MOVE_FIELD = 'MOVE_FIELD';
export const UPDATE_SECTION_ITEM = 'UPDATE_SECTION_ITEM';
export const FETCH_PREVIEW_DATA_PENDING = 'FETCH_PREVIEW_DATA_PENDING';
export const FETCH_PREVIEW_DATA_SUCCESS = 'FETCH_PREVIEW_DATA_SUCCESS';
export const FETCH_PREVIEW_DATA_ERROR = 'FETCH_PREVIEW_DATA_ERROR';
export const SAVE_DATA_SUCCESS = 'SAVE_DATA_SUCCESS';
export const SAVE_DATA_PENDING = 'SAVE_DATA_PENDING';
export const SAVE_DATA_ERROR = 'SAVE_DATA_ERROR';
export const TOGGLE_CONFIG_MODAL = 'TOGGLE_CONFIG_MODAL';
export const TOGGLE_CHARTS_MODAL = 'TOGGLE_CHARTS_MODAL';
export const UPDATE_OBJECT_PROPERTY = 'UPDATE_OBJECT_PROPERTY';
export const UPDATE_FIELD_LIST = 'UPDATE_FIELD_LIST';
export const ADD_CHART = 'ADD_CHART';
export const REMOVE_CHART = 'REMOVE_CHART';
export const ZOOM_CHART = 'ZOOM_CHART';
export const APPLY_TABLE_FILTER = 'APPLY_TABLE_FILTER';
export const SET_CHARTS_DATA = 'SET_CHARTS_DATA';
export const SET_CURRENT_VIEW = 'SET_CURRENT_VIEW';
export const SET_EDIT_CHART_INDEX = 'SET_EDIT_CHART_INDEX';
export const EDIT_CHART = 'EDIT_CHART';
export const SET_ORDER_COLUMNS = 'SET_ORDER_COLUMNS';
export const SET_IS_OPEN_MODAL_FORMULA = 'SET_IS_OPEN_MODAL_FORMULA';
export const SET_MODE_MODAL_FORMULA = 'SET_MODE_MODAL_FORMULA';
export const SET_EDIT_MODE_DATA_MODAL_FORMULA = 'SET_EDIT_MODE_DATA_MODAL_FORMULA';
export const SET_INDEX_MODAL_FORMULA = 'SET_INDEX_MODAL_FORMULA';

export const setSortColumns = (column,values) => dispatch => {
  dispatch({
    type: SET_ORDER_COLUMNS,
    column,
    values
    
  })

  return Promise.resolve();
}

export const fetchPreviewDataPending = (timeout_id) => {
  return {
    type: FETCH_PREVIEW_DATA_PENDING,
    timeout_id
  }
}

export const fetchPreviewDataSuccess = (data) => {
  return {
    type: FETCH_PREVIEW_DATA_SUCCESS,
    data: data
  }
}

export const fetchPreviewDataError = (error) => {
  return {
    type: FETCH_PREVIEW_DATA_ERROR,
    error: error
  }
}

export const addField = (section, field) => {
  return {
    "type": ADD_FIELD,
    section,
    field
  }
}

export const removeField = (section, index) => {
  return {
    "type": REMOVE_FIELD,
    section,
    index
  }
}


export const addChart = (chart_def) => {
  return {
    "type": ADD_CHART,
    chart_def
  }
}

export const removeChart = (index) => {
  return {
    "type": REMOVE_CHART,
    index
  }
}

export const zoomChart = (chart_def) => {
  return {
    "type": ZOOM_CHART,
    chart_def
  }
}

export const setDraggingField = (dragging) => {
  return {
    "type": SET_DRAGGING_FIELD,
    "dragging": dragging
  };
};

export const moveField = (result) => {
  return {
    "type": MOVE_FIELD,
    "result": result
  };
};

export const toggleConfigModal = () => {
  return {
    "type": TOGGLE_CONFIG_MODAL,
  };
};

export const toggleChartsModal = (dashlet_id) => {
  return {
    "type": TOGGLE_CHARTS_MODAL,
    dashlet_id
  };
};

export const updateSectionItem = (section, item_index, property, value) => {
  return {
    "type": UPDATE_SECTION_ITEM,
    section,
    item_index,
    property,
    value
  }
}

export const saveDataPending = () => {
  return {
    type: SAVE_DATA_PENDING
  }
}

export const saveDataSuccess = (data) => {
  return {
    type: SAVE_DATA_SUCCESS,
    data: data
  }
}

export const saveDataError = (error) => {
  return {
    type: SAVE_DATA_ERROR,
    error: error
  }
}

export const updateObjectProperty = (object_name, property, value) => {
  return {
    "type": UPDATE_OBJECT_PROPERTY,
    object_name,
    property,
    value
  }
}

export const updateFieldList = (field_list) => {
  return {
    "type": UPDATE_FIELD_LIST,
    field_list
  }
}

export const applyTableFilter = (column, value) => {
  return {
    "type": APPLY_TABLE_FILTER,
    column,
    value
  }
}

export const setChartsData = (data) => {
  return {
    type: SET_CHARTS_DATA,
    data
  }
}


export const setCurrentView = (current_view) => {
  return {
    type: SET_CURRENT_VIEW,
    current_view
  }
}

export const setEditChartIndex = (chart_index) => {
  return {
    type: SET_EDIT_CHART_INDEX,
    chart_index
  }
}

export const editChart = (index, chart_def) => {
  return {
    "type": EDIT_CHART,
    index,
    chart_def
  }
}

export const setIsOpenModalFormula = isOpen => {
  return {
    "type": SET_IS_OPEN_MODAL_FORMULA,
    payload: {
      isOpen
    }
  }
}

export const setModeModalFormula = mode => {
  return {
    "type": SET_MODE_MODAL_FORMULA,
    payload: {
      mode
    }
  }
}

export const setEditModeDataModalFormula = data => {
  return {
    "type": SET_EDIT_MODE_DATA_MODAL_FORMULA,
    payload: {
      edit_mode_data: data
    }
  }
}

export const setIndexModalFormula = index => {
  return {
    "type": SET_INDEX_MODAL_FORMULA,
    payload: {
      index
    }
  }
}